export function createVVID() {
  let getCharAt = count => {
    let $charAt = "",
      ALPHA_CHARS = "0123456789abcdef";
    for (let i = 0; i < count; i++) {
      $charAt += ALPHA_CHARS.charAt(
        (Math.random() * (ALPHA_CHARS.length - 1)) >> 0
      );
    }
    return $charAt;
  };
  let arr = [];
  arr.push(getCharAt(8));
  for (let i = 0; i < 3; i++) {
    arr.push(getCharAt(4));
  }
  let time = new Date().getTime();
  arr.push(("0000000" + time.toString(16)).substr(-8) + getCharAt(4));
  return arr.join("");
}

