<template>
    <div>
        <div class="openMemberModalTabWrap type_1" v-if="type == 'type_1'">
            <div class="tab" v-for="(item,index) in list" :class="{active : (getIndex === index)}" :key="index" @click="select(item)">
                <slot :data="item">

                </slot>
            </div>
        </div>
        <div class="openMemberModalTabWrap type_2" v-if="type == 'type_2'">
            <div class="swiper-container"  ref="swiper"  >
                <div class="swiper-wrapper">
                    <div class="swiper-slide slideItem"
                         v-for="(item,index) in list"
                         :key="index"
                         :style="{backgroundImage : item.wider?`url(${item.img})`:''}" 
                         :class="{active : (getIndex === index),wider : item.wider,recommend :item.recommend }"
                         @click="select(item)">
                        <div class="tab">
                            <slot :data="item">

                            </slot>
                        </div>
                    </div>
                </div>
            </div>

            <i class="next action" @click="next" ref="next">

            </i>
            <i class="pre action"  @click="pre"  ref="pre">

            </i>
        </div>
        <div class="openMemberModalTabWrap type_3"  v-if="type == 'type_3'">
            <div class="tab" v-for="(item,index) in list" :class="{active : (getIndex === index)}" :key="index" @click.native="select(item)">
                <slot :data="item">

                </slot>
            </div>
        </div>
    </div>
</template>
<style lang="less" scoped>
    @import "~Swiper3Wrap/swiper.css";
    .openMemberModalTabWrap{
        width: 100%;
        box-sizing: border-box;
        .tab{
            cursor: pointer;
        }
        &.type_1{
            height: 74px;
            &:after{
                display: block;
                clear: both;
                content : "";
            }
            .tab{
                float: left;
                padding: 15px 20px;
                box-sizing: border-box;
                width : 50%;
                position: relative;
                &.active{
                    &:before{
                        background: #C19E00;
                        display: block;
                        content : "";
                        height: 2px;
                        width : 340px;
                        position: absolute;
                        bottom : 20px;
                        left : 20px;
                    }
                }
            }
        }
        &.type_2{
            position: relative;
            height: 102px;
            box-sizing: border-box;
            .swiper-container{
                padding: 10px;
                margin: 0 20px;
                position: relative;
                &::after{
                    display: block;
                    content: "";
                    clear : both;
                }
                // width: auto;
                .slideItem{
                    position: relative;
                    height: 80px;
                    width : 160px;
                    border: 1px solid #ECECEC;
                    box-shadow: 0 2px 8px 0 rgba(0,0,0,0.05);
                    border-radius: 8px;
                    display: table;
                    background-size: contain;
                    background-position: center;
                    background-repeat: no-repeat;
                    &.wider{
                        width : 240px;
                    }
                    .tab{
                        display: table-cell;
                        vertical-align: middle;
                        width: 100%;
                    }
                    &.active{
                        border-color: #C19E00;
                        &:after{
                            display: block;
                            content: "";
                            position: absolute;
                            right : 0;
                            bottom: 0;
                            width : 36px;
                            height: 36px;
                            background: url("~@/openMemberModal/select.png") no-repeat center;
                            background-size: contain;
                        }
                    }
                    &.recommend{
                        &:before{
                            display: block;
                            content: "";
                            position: absolute;
                            right : -6px;
                            top : -8px;
                            width : 50px;
                            height: 22px;
                            background: url("~@/openMemberModal/recommend.png") no-repeat center;
                            background-size: contain;
                        }
                    }
                }
            }
            .action{
                display: block;
                height: 100%;
                position: absolute;
                width: 16px;
                top : 0;
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
                cursor: pointer;
                z-index: 2;
                &.next{
                    background-image: url("~@/openMemberModal/ar_r.png");
                    right: 2px;
                }
                &.pre{
                    background-image: url("~@/openMemberModal/ar_l.png");
                    left : 2px;
                }
            }
        }
        &.type_3{
            .tab{
                width: 100%;
                height:60px;
                box-sizing: border-box;
                padding: 19px 25px;
                &.active{
                    background: white;
                }
            }
        }
    }
</style>
<script>
    import Swiper from "Swiper3Wrap/swiper"
    export default {
        props : ["type","list","idKey","value"],
        data(){
            return {
                swiper : "",
            }
        },
        watch : {
            "list" : {
                immediate : true,
                deep : true,
                handler(n){
                    if(n&&n.length){
                        let _index = this.getIndexById()
                        if(_index === undefined || _index ===null || _index ==="" ){
                            var key = this.idKey||'';
                            if(key){
                                this.$emit("input",this.list[0][key]);
                            }
                            else{
                                this.$emit("input",this.list[0]);
                            }
                        }

                        if(this.type == "type_2"){
                            this.$nextTick(()=>{
                                this.initSwiper()
                            })
                        }
                    }
                }
            }
        },
        mounted (){
            this.$nextTick(()=>{
                this.initSwiper()
            })
        },
        computed : {
            getIndex(){
                return (this.getIndexById() || 0);
            }
        },
        methods : {
            next(){
                this.swiper&&this.swiper.slideNext()
            },
            pre(){
                this.swiper&&this.swiper.slidePrev()
            },
            initSwiper(){
                console.log("-------------")
                if(this.swiper){
                    console.log("++++++++++++++++")
                    this.swiper.slideTo(0, 0, false);//切换到第一个slide，速度为1秒
                    this.swiper.update();
                }
                else{
                    this.swiper = new Swiper(this.$refs.swiper, {
                        loop : false,
                        slidesPerView : "auto",
                        spaceBetween : 20,
                        initialSlide : (this.getIndexById() || 0),
                        // prevButton : this.$refs.pre,
                        // nextButton : this.$refs.next,
                    })
                }
            },
            getIndexById(){
                var key = this.idKey||'';
                let index = "";

                for(let i = 0 ; i < this.list.length; i ++){
                    var value = key?this.list[i][key]:this.list[i];

                    if(this.value == value){
                        index = i;
                        break;
                    }
                }

                return index;
            },
            select(item){
                var key = this.idKey||'',
                    newVal = "",
                    oldVal = this.value;
                if(key){
                    newVal = item[key];
                }
                else{
                    newVal = item;
                }

                if(newVal !== oldVal){
                    this.$emit("input",newVal);
                }
            }
        }
    }
</script>