<template>
  <div class="left-img-right-text-more" v-if="comp.data">
    <div class="content">
        <ul>
            <li class="item" v-for="(item,index) in onlyShowTen(comp.data)" :key="index" :title="item.name" @mouseenter="toShow($event,item)" @mouseleave="toShow($event,item)" :data-index="index">
                <div v-if="show(index,item)" class="item-mouseenter">
                    <div class="inner-left">
                        <div class="pic" @click="toJump(item.action)">
                            <img :src="getImg(item)" alt="">
                        </div>
                        <div :class="getBgClass(index)">{{index+1}}</div>
                    </div>
                    <div class="inner-right">
                        <div class="video-name" v-if="item.name" v-html="item.name" @click="toJump(item.action)">创业时代</div>
                        <div class="video-actors oneline" v-if="item.actors" v-html="getActorsText(item.actors)">
                            在一次偶然的机会软件工程师郭鑫年找到了新的灵感
                        </div>
                        <div class="video-des" v-if="item.detail">
                            {{item.detail}}
                        </div>
                    </div>
                </div>
                <div v-else :class="['item-normal',{'jishu': iseven(index)}]">
                    <div class="item-title"><span class="r-index">{{index+1}}</span>{{item.name}}</div>
                </div>
            </li>
        </ul>
    </div>
  </div>
</template>
<style lang="less">
    @import "../../../css/lib/leftImgRightTextMore.less";
</style>
<script>
import {mapState, mapGetters, mapActions, mapMutations} from 'vuex'
import actionHandle from 'common/actionHandle'
export default {
    props:["comp","groupID"],
    data(){
        return {
            showIndex:0,
            interTimer:-1
        };
    },
    mounted(){
        // console.log(this.comp,'22222222222');
    },
    methods:{
        show(index,item){
            let bl = (this.showIndex == index);
            //初始化
            if(bl && this.showIndex ==0 && this.interTimer==-1 && !item.actors){
                this.$store.dispatch("getProgramInfo",{groupid:this.groupID,compid:this.comp.id,cid:item.pID});
            }
            return bl;
        },
        getImg(item){
            let url = '';
            try{
                url = item.h5pics.highResolutionV;
            }catch(e){
                url = ''
            }
            return url;
        },
        toShow(event,item){
            let val = parseInt(event.target.getAttribute("data-index"));
            if(event.type == "mouseenter"){
                if(this.interTimer && this.showIndex == val) {
                    return ;
                }else{
                    if(this.interTimer>-1){
                        clearTimeout(this.interTimer);
                        this.interTimer = -1;
                    }
                    this.interTimer = setTimeout(()=>{
                        this.showIndex = val;
                        if(!item.actors)
                            this.$store.dispatch("getProgramInfo",{groupid:this.groupID,compid:this.comp.id,cid:item.pID});
                    },100);
                }
            }
            
        },
        getBgClass(index) {
            if(index<3){
                return "badge three";
            }else {
                return "badge other";
            }
        },
        iseven(index){
            let bl = index%2 == 0?false:true;
            // console.log(bl);
            return bl;
        },
        getActorsText(actors){
            let str = this.styleArr(actors,'/');
            return str;
        },
        styleArr(value,flg){
            if(!value)return [];
            let arr = value.split(' ');
            let newArr = arr.map((item,index)=>{
                if(item != ""){
                    if(index<arr.length-2){
                        return item + flg
                    }else{
                        return item
                    }
                }
            }).filter(item=>{
                if(item){
                    return item
                } 
            })
            return newArr.join("");
        },
        toJump(action){
            actionHandle(action);
        },
		onlyShowTen(data) {
			var realData = data;
			if(data&&data.length >=10) {
				realData = data.slice(0,10);
			}
			return realData
		}
    }

}
</script>

