<template>
    <tab class="memberTypeTabWrap" type="type_1" :list="_list" idKey="id" :value="value"
         @input="input">
        <div  slot-scope="{data}" class="memberTypeTab" :class="{active : value == data.id}">
            <i :class="data.class">
            </i>
            <span class="title">
                    {{data.title}}
            </span>
            <span class="subTitle">
                    {{data.subTitle}}
            </span>

        </div>
    </tab>
</template>
<style scoped lang="less">
    @import "~css/personalCenter";
    .memberTypeTabWrap{
        box-shadow: 0px -5px 20px #f2f2f2;
        .memberTypeTab{
            text-align: center;
            padding : 10px 20px;
            i{
                display: inline-block;
                width : 16px;
                height: 16px;
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
                margin-right: 3px;
                &.video{
                    background-image: url("~@/openMemberModal/videoMember.png");
                }
                &.sport{
                    background-image: url("~@/openMemberModal/sportMember.png");
                }
            }
            >*{
                display: inline-block;
                vertical-align: middle;
            }
            span{
                &.title{
                    font-size: 16px;
                    color: #323232;
                }
                &.subTitle{
                    font-size: 12px;
                    color: #B2B2B2;
                }
            }
            &.active{
                span{
                    &.title{
                        color: #C19E00;;
                    }
                    &.subTitle{
                        color: #E4CF6E;
                    }
                }
                i{
                    &.video{
                        background-image: url("~@/openMemberModal/videoMemberActive.png");
                    }
                    &.sport{
                        background-image: url("~@/openMemberModal/sportMemberActive.png");
                    }
                }
            }

        }
    }
</style>
<script>
    import tab from "components/openMemberModal/common/tab"
    export default {
        props : ["value","list"],
        data(){
            return{
            }
        },
        computed : {
            _list(){
                return this.list||[]
            }
        },
        components : {
            tab
        },
        methods : {
            input(val){
                this.$emit("input",val);
            }
        }

    }
</script>