<template>
    <!-- <div :class="['header-wrapper', { 'fixed bg-white': headerFixed && !sideBox.nav, 'rel bg-white': relHeader, 'bg-black': relbbHeader, 'fixed fixed-bg': headerFixed && sideBox.nav}]" v-if="config"> -->
    <div :class="['header-wrapper', {'fixed bg-white': headerFixed && !sideBox.nav, 'fixed fixed-bg': (headerFixed && sideBox.nav) || sideBox.nav}]" v-if="config">
        <div class="header-inner" style="height: 60px;">
            <div class="logo">
                <div class="icon" @click="jumpIndex()"></div>
                <div :class="['header-nav', {'hidden': !sideBox.nav && headerFixed, 'display': (sideBox.nav && headerFixed) || !sideBox.nav || sideBox.nav}]" v-if="isPage">
                    <a :class="['header-nav-item', {'color': sideBox.nav, 'text-color': headerFixed && !sideBox.nav, 'default': !flag && !mark}]" v-for = "(nav,index) in nList" :href="nav.link" :key="index" @click.stop="detailToOther(nav.pageId)">
                        <span class="nav-text" v-if="nav.text">{{nav.title}}</span>
                        <div class="nav-all" v-if="nav.icon" @click.stop="showNav($event)">
                            <div class="all-icon">
                                <i :class="['icon-all', {'hand-close': sideBox.nav}]"></i>
                            </div>
                        </div>
                    </a>
                </div>
            </div>
            <div class="search-wrapper">
                <div class="search-box">
                    <div class="box-left">
                        <input @blur="setSearchpopup($event)" @click="setSearchpopup($event)" @input="handleInput($event)" @keyup.enter="toSearch()" :placeholder="getHotKey()" v-model="keywords" type="text" class="search-box-input">
                    </div>
                    <div class="box-mid" @click="linkHotPage()">
                        <span class="hot-icon"></span>
                        <span class="hot-txt">热播榜</span>
                    </div>
                    <div class="box-right" @click="toSearch()">
                        <span class="search-icon"></span>
                        <a class="search-txt">搜索</a>
                    </div>
                </div>
                <div class="search-popup " v-if="search.show">
                    <search-drop-down :hotResult="hotResult" :historyResult="historyResult" :searchResult="searchResult"></search-drop-down>
                </div>
            </div>
            <div class="side-wrapper">
                <div class="side-item header-vip">
                    <div class="icon-item vip" @click="openVip($event)">
                        <span class="icon "></span>
                        <span class="text">VIP</span>
                    </div>
                </div>
                <div class="side-item header-history" @mouseenter="goHistory($event)" @mouseleave="goHistory($event)">
                    <div class="icon-item history">
                        <span class="icon"></span>
                        <span class="text">历史</span>
                    </div>
                    <div class="history-popbox" v-show="sideBox.history" >
                        <PlayHistory :playHistorys="playHistorys" @directPlayCenter="directPlayCenter" @login="switchLogin"></PlayHistory>
                    </div>
                </div>
                <div class="side-item header-download">
                    <div class="icon-item download" @click="downloadClient(action)">
                        <span class="icon "></span>
                        <span class="text">下载客户端</span>
                    </div>
                </div>
                <div class="side-item header-user" @mouseenter="openUserBox($event)" @mouseleave="openUserBox($event)" @click="goLogin()">
                    <div class="icon-item user" >
                        <img :src="getUserImg()"/>
                    </div>
                    <div class="login-popbox" v-show="sideBox.user">
                        <LoginBox :userData="userData" @switchLogin="switchLogin()" @openSportMember="openSportMember"></LoginBox>
                    </div>
                </div>
            </div>
        </div>
        <transition name="fade">
            <div class="channel-children" v-show="sideBox.nav">
                <ul>
                    <li v-for="(item, index) in publicData" :key="index" :style="{background: 'url(' + item.icon + ') no-repeat center/100% 100%'}" @click="jumpPage(item)">
                        <span>{{item.title}}</span>
                    </li>
                </ul>
            </div>
        </transition>
        <open-member-modal :value="modalStatus" @input="memberModalAction" :memberConfig="memberConfig"></open-member-modal>
    </div>
</template>

<style lang="less">
    @import "../../../css/group/header.less";
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s
    }
    .fade-enter, .fade-leave-active {
        opacity: 0
    }
</style>

<script>
import openMemberModal from "components/openMemberModal/openMemberModal"
import {mapState, mapGetters, mapActions,mapMutations } from 'vuex';
import searchDropDown from "./searchDropDown";
import defaultUserPhoto from '@/defaultUserPhoto.png';
import PlayHistory from "./playhistory";
import LoginBox from "./loginbox";
import utils from 'utils/utils';
import actionHandle, {commonHandle} from 'common/actionHandle';
import webCommon from 'common/webCommon.js';
import EventBus from 'utils/bus.js';
import userInfo from "userInfo";
import "css/common/toast"
import Toast from "components/personalCenter/common/toast"
import KSStatistics from "KSStatistics"

import vue from "vue";
vue.prototype.$Toast = Toast;

export default {
    name: "indexHeader",
    props: {
    	"data" : {
    		type : Object,
            default : null
        },
        "userData" : {
            type : Object,
            default : null
        },
        // "headerNavList": {
        //     type : Array,
        //     default : null
        // },
        "from": {
            type : String,
            default : null
        },
        "markNav": {
            type: Object,
            default: null
        },
        "refreshUserStore" : {
            type : Boolean,
            default : true
        },
        "text" : {
            type : String,
            default : ''
        }
    },
    data(){
        return {
            search:{
                show:false,
                hot:"延禧攻略",
                content:""
            },
            defaultUserPhoto,
            sideBox:{
                vip:false,
                history:false,
                user:false,
                nav:false
            },
            headerFixed:false,
            // 实现搜索框 和 url栏中keywords的同步
            keywords : "",
            flag: (location.href.indexOf('index') != -1) && (location.href.indexOf('pageId') == -1), // 表示首页
            mark: (location.href.indexOf('cid') != -1), // 表示详情页
            channel: (location.href.indexOf('index') != -1) && (location.href.indexOf('pageId') != -1) // 表示频道页
        }
    },
    components: {
        searchDropDown,
        PlayHistory,
        LoginBox,
        openMemberModal
    },
    computed: {
        ...mapState({
            config: state => state.header.config,
            playHistorys: state => state.history.playHistorys,
            navList: state => state.header.navList,
            searchResult: state => state.header.searchResult,
            hotResult: state => state.header.hotResult,  
            historyResult: state => state.header.historyResult,
            headerNavList: state => state.header.headerNavList,
            modalStatus : state => state.header.modalStatus,
            memberConfig : state => state.header.memberConfig,
            publicData: state => state.header.publicData
        }),
        ...mapState('downloadLink',['action']),
        ...mapGetters('header', {
            logo: 'logo'
        }),
         // 是否登录
        isLogin() {
            return this.userData && this.userData.isLogin;
        },
        // 用户信息
        userInfo() {
            if (!this.isLogin) {
                return null;
            }
            return this.userData && this.userData.userInfo
                ? this.userData.userInfo
                : null;
        },
        nList(){
            if(this.headerNavList && this.headerNavList.length>0){
                return this.headerNavList;
            }
            // return this.navList
        },
        relbbHeader(){
            if(this.from) {
                switch(this.from){
                    case "detail":
                        return "bg-black";
                }
            }
            return null;
        },
        relHeader(){
            return this.nList && this.nList.length;
        },
        isPage() {
            if ((this.headerFixed && this.flag) || this.mark || this.channel) {
                return true;
            }
            if (!this.flag && !this.mark && !this.channel) {
                return true;
            }
            return false;
        }
    },
    methods: {
        ...mapMutations("header",["openMemberModalAction","updateMemberConfig"]),
        memberModalAction(val){
            this.openMemberModalAction(val);
        },
        registerHashChange(){
            if (utils.isIE()) {
                window.addEventListener('hashchange', () => {
                    let currentPath = window.location.hash.slice(1);
                    if (this.$route&&this.$route.path !== currentPath) {
                        this.$router.push(currentPath)
                    }
                }, false)
            }
        },
        openSportMember(){
            this.memberModalAction(true)

            this.updateMemberConfig({
                goodsType : 1,
                memberType : 2
            })
        },
        directPlayCenter(){
            location.href = "personalCenter.html#/history"
        },
        openVip(event) {
            if(this.isLogin){
                this.memberModalAction(true)

                this.updateMemberConfig({
                    goodsType : 5,
                    memberType : 1
                })
            }
            else{
                this.switchLogin();
            }
        },
        goHistory(event) {
            if(event.type == "mouseenter"){
                this.sideBox.history = true;
            }else {
                this.sideBox.history = false;
            }
            
        },
        downloadClient(action) {
            commonHandle('downloadUrl',action.params.url)
        },
        openUserBox(event) {
            if(event.type == "mouseenter"){
                this.sideBox.user = true;
            }else {
                this.sideBox.user = false;
            }
        },
        goLogin(){
            if(!this.isLogin){
                this.$emit("showLogin");
            }
            else{
                location.href = "personalCenter.html"
            }
        },
        getUserImg(){
            if(this.userInfo && this.userInfo.picture){
                return this.userInfo.picture;
            }else return defaultUserPhoto;
        },
        switchLogin(){
            this.$emit("showLogin");
        },
        handleScroll () {
            let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
            // let eleHeader =  document.querySelector('.header-wrapper');
            let carousel = document.querySelector('.slide-bg');
            let eleNav = document.querySelector('.navBar-wrapper');
            let offsetTop = 0 ;
            if(eleNav) {
                // offsetTop = ele.offsetTop;
                // offsetTop = eleNav.offsetTop + eleNav.clientHeight;
                offsetTop = carousel.offsetHeight + eleNav.offsetHeight;
            } else if (carousel){
                offsetTop = carousel.offsetHeight;
            } else {
                offsetTop = 400;
            }
            if (scrollTop > offsetTop) {
                if(!this.headerFixed) this.headerFixed = true;
            } else {
                if(this.headerFixed) this.headerFixed = false;
                this.sideBox.nav = false;

            }
        },
        showNav(event) {
            // console.log('header99999999999999999999999');
            this.sideBox.nav = !this.sideBox.nav;
            if(this.publicData) return;
            this.$store.dispatch('header/getChannelData');
            // if(event.type == "mouseenter"){
            //     this.sideBox.nav = true;
            // }else {
            //     this.sideBox.nav = false;
            // }
        },
        getSRdata(){
            this.$store.dispatch('header/getHotResult');
        },
        handleInput($event){
            let val = $event.target.value;
            // var s = utils.HtmlUtil.htmlEncodeByRegExp(val);
            this.search.content = val; 
            // console.log(val);
            if(!val) {
                this.$store.commit('header/set_key_value',{key:"searchResult",value:null});
                return;
            }
            this.$store.dispatch('header/getSearchTipResult',val);
        },
        toSearch(){
            var val;
            if(this.keywords) {
                val = this.keywords;
            } else {
                val = this.search.content || this.config.defaultSearchTxt || "火王1";
            }
            this.$store.commit('header/setHistoryResult',val);
            this.$store.commit('header/toSearch',val);
        },

        // showKeyWords() {
        //     let string = window.location.pathname;
        //     // console.log(string,'string')
        //     var words;
        //     if(string.indexOf('search') != -1) {
        //         words = window.sessionStorage.getItem('keywords') || ''
        //     }
        //     else {
        //         words = ''
        //     }
        //     // console.log(words,'words')
        //     return words
        // },

        /**获取搜索结果的 */
        getHotKey(){
            if(this.config){
                // console.log(this.config.defaultSearchTxt,'ooooooooo');
                return this.config.defaultSearchTxt || "火王1";
            }
        },
        setSearchpopup(){
            // console.log(event,'event')
            if(event.type === 'click') this.search.show = true;
            else this.search.show = false;
        },

        // closeSearchpopup(event) {
        //     this.search.show = false; 
        //     console.log(111111)
        //     // setTimeout(function() {
        //     //     this.search.show = false; 
        //     // }.bind(this),250) 
        // }
        // 跳转首页
        jumpIndex() {
            var pageId = webCommon.INDEX_PAGE_ID;
            // console.log(webCommon,'33333333333')
            commonHandle(pageId)
        },
        // 跳转频道页
        jumpPage(item) {
            if (item.title == '体育') {
                window.open(item.action.params.url);
            } else if (item.title == '片库') {
                commonHandle(webCommon.LIBRARIES_PAGE_ID);
            } else {
                commonHandle(webCommon.INDEX_PAGE_ID, {pageId: item.action.params.path});
            }
        },
        detailToOther(pageId) { // 详情页跳转到三个子频道
            if (pageId) {
                commonHandle(webCommon.INDEX_PAGE_ID, {pageId: pageId});
            }
        },
        linkHotPage(){
            commonHandle(webCommon.HOT_RANK_PAGE_ID, {param: 1});
        }
    },
    created () {
        // console.log("header模块渲染");
        this.registerHashChange();
        // 切换账号完成之后重新获取会员信息
        window.addEventListener('message',e=>{
            if(e && e.data && (typeof e.data) === 'string'){
                try{
                    let d1 = JSON.parse(e.data);
                    if(d1.type == "login"){
                        if(this.refreshUserStore){
                            this.$store.commit("userdata/updateUserInfo");
                            this.$store.dispatch('history/getPlayHistory', {
                                start: 0,
                                limit: 100,
                                all: 1,
                                prdInfoIds: 1002601
                            });
                        }
                        else{
                            userInfo.initUserData();
                            userInfo._init();
                        }
                        // 清除批价的缓存
                        EventBus.$emit("clearCache");
                        KSStatistics.setUserInfoParam();
                        // 重新获取会员信息
                        this.$store.dispatch("userdata/getMemberInfo",false).then(res=>{
                            this.$emit("memberInfoRegain",res);
                        })
                    }
                }catch(e){
                    
                }
                
            }
        });
        // utils
        //     .cookie()
        //     .setItem(
        //         'userInfo',
        //         '{%22userId%22:%2220229296%22%2C%22userToken%22:%224346D1AA8F1E115F0D78%22%2C%22clientId%22:%2279acd784-cbbb-4cae-8778-8723e001164b%22%2C%22carrierCode%22:%22CM%22%2C%22sname%22:%2215021112084%22%2C%22mobile%22:%2215021112084%22%2C%22picture%22:%22http://img.cmvideo.cn:8080/publish/voms2/uic_service/picture/userImage/20/229/296/20181115160107evkl.png%22%2C%22sex%22:2%2C%22ssotoken%22:%22STnid00000015453545433841gBo8cA0LiPf6s0wpmfT2hsxEETL409x%22}'
        //     );
        
        this.$store.dispatch('header/getConfig');
        this.$store.dispatch('header/getHotResult');
        this.$store.dispatch('history/getPlayHistory', {
            start: 0,
            limit: 100,
            all: 1,
            prdInfoIds: 1002601
        });
        
        // 实现url栏中的keywords 和 input框 同步
        var k_words = utils.getQueryString('keywords') || this.search.content
        this.keywords = utils.HtmlUtil.htmlEncodeByRegExp(k_words)
    },
    watch: {
        markNav(newValue, oldValue) {
            if (newValue.flag == true) {
                this.sideBox.nav = false;
            }
        }
    },
	mounted(){
        if(!this.nList){
            window.addEventListener('scroll', this.handleScroll);
        } 
        this.$store.commit("header/setHeaderNavData", {pageId: utils.getQueryString("pageid"), flag: location.href.indexOf('index') != -1, cid: utils.getQueryString("cid"), sports: location.href.indexOf('sport') != -1, personal:location.href.indexOf('personal') != -1, search: location.href.indexOf('search') != -1, channelLibrary: location.href.indexOf('channelLibrary') != -1, rank: location.href.indexOf('rank') != -1, dataList: location.href.indexOf('dataList') != -1, teamDetail: location.href.indexOf('teamDetail') != -1, val: null, deliver: this.text});
        EventBus.$on('openVip',(e)=>{
            this.openVip(e)
        })
    },
    destroyed () {
        if(!this.nList){
            window.removeEventListener('scroll', this.handleScroll)
        }
    }
}
</script>