<template>
    <div class="openMemberModalToPayDetailWrap">
        <div class="QRCode" v-if="type == 'QRCode'">
            <i>
                <q-r-code :scanUrl="scanUrl"></q-r-code>
            </i>
            <p>
                支付宝/微信支付
            </p>
        </div>
        <div class="QRCode fixQRCode" v-if="type == 'fixQRCode'">
            <i class="">

            </i>
            <p>
                手机扫描二维码
            </p>
        </div>
        <div class="HF"  v-if="type == 'HF'">
            <div class="telWrap">
                <div class="tel">
                    <p>
                        {{tel}}
                    </p>
                    <div>
                        手机号
                    </div>
                </div>
            </div>
            <div class="checkStatus">
                <p-check-box info="同意" v-model="agree" type="checkBox_2"></p-check-box>
                <span @click="open('staticText.html#/memberServeAgreement')">
                    《会员服务协议》
                </span>
            </div>
            <div class="payBtn" @click="toPay" :style="{opacity : agree?1:0.5}">
                确认支付
            </div>
        </div>
        <div class="agreement" v-if="isContinuous">
            <h5>
                连续包月说明：
            </h5>
            <ul>
                <li v-for="(item,index) in ruleList" :key="index">
                    {{item.name}}
                </li>
            </ul>
            <div class="agreementLink"  @click="open('staticText.html#/monthlyPayAgreement')">
                《连续包月服务规则》
            </div>
        </div>
    </div>
</template>
<style lang="less" scoped>
    .openMemberModalToPayDetailWrap{
        padding : 50px;
        box-sizing: border-box;
        width : 100%;
        text-align: center;
        >*{
            display: inline-block;
            vertical-align: middle;
            margin: 0 20px;
        }
        .QRCode{
            width: 160px;
            &.fixQRCode{
                i{
                    background: url("~@/openMemberModal/fixQRCode.png") no-repeat  center;
                    background-size: contain;
                }
            }
            i{
                display: block;
                height: 160px;
                width: 100%;
                position: relative;
            }
            >p{
                font-size: 16px;
                color: #4D4D4D;
                text-align: center;
                margin-top: 5px;
            }
        }
        .HF{
            width: 200px;
            .telWrap{
                display: table;
                width: 100%;
                height: 94px;
                background: url("~@/openMemberModal/hf_bg.png") no-repeat center;
                background-size: contain;
                margin-bottom: 15px;
                .tel{
                    display: table-cell;
                    vertical-align: middle;
                    text-align: center;
                    >p{
                        font-size: 16px;
                        color: #333333;
                    }
                    >div{
                        font-size: 12px;
                        color: #B2B2B2;
                        margin-top: 5px;
                    }
                }
            }
            .checkStatus{
                >span{
                    font-size: 12px;
                    color: #E4CF6E;
                    margin-left: 3px;
                    display: inline-block;
                    vertical-align: middle;
                    cursor: pointer;
                }
            }
            .payBtn{
                background: url("~@/openMemberModal/telPayBtn.png") no-repeat center;
                background-size: contain;
                box-shadow: 0 1px 4px 0 rgba(129,114,88,0.30);
                font-size: 12px;
                color: #FFFFFF;
                width: 150px;
                cursor: pointer;
                height: 30px;
                text-align: center;
                line-height: 30px;
                margin: 15px auto;
                border-radius: 15px;
            }
        }
        .agreement{
            width : 200px;
            text-align: left;
            h5{
                font-size: 14px;
                color: #666666;
                margin-bottom: 10px;
            }
            ul{
                color: #666666;
                margin-bottom: 6px;
                li{

                }
            }
            .agreementLink{
                cursor: pointer;
                margin-bottom: 5px;
                font-size: 12px;
                color: #B2B2B2;
            }
        }
    }
</style>
<script>
    import PCheckBox from "components/personalCenter/common/PCheckBox"
    import QRCode from "components/personalCenter/common/QRCode"
    import {mapState,mapMutations,mapActions}  from "vuex"
    export default {
        props : ["type","isContinuous","scanUrl","payUrl","info"],
        data(){
            return {
                agree : true,
                ruleList : [
                    {name : ""},
                    {name : "2、会员到期前1天为您自动续费,会员有效期延长 1个月；"},
                    {name : "3、您可随时取消连续包月服务，取消后不会再自动续费。"},
                ]
            }
        },
        computed:{
            ...mapState("userdata",["isLogin","userInfo","userHeaderInfo","memberInfo"]),
            tel(){
                return this.isLogin&&this.userInfo&&this.userInfo.mobile;
            },
        },
        watch : {
            info : {
                deep : true,
                immediate : true,
                handler(n,o){
                    if(n){
                        var str = "1、连续包月会员尊享 price元/月；";
                        this.ruleList[0].name = str.replace(/price/g,n.pricing);
                    }
                }
            }
        },
        components : {
            PCheckBox,
            QRCode
        },
        methods : {
            open(url){
                window.open(url)
            },
            toPay(){
                if(this.agree){
                    if(this.payUrl){
                        location.href = this.payUrl
                    }
                    else{
                        alert("下单失败")
                    }
                }
            }
        }
    }
</script>