import { fetch, delet } from 'utils/axios';
import api from 'common/api';
import userInfo from 'userInfo';
import _ from 'lodash';
import { isToday, isOffsetDay, isYesterday } from 'utils/time_func';
import { formatDate,formatTime } from 'utils/format';

const icon_history_web_arr = ['0001', '0011', '0012', '0013', '0014', '0015', '0016'];
const icon_history_h5_arr = [
    '0002',
    '0003',
    '0004',
    '0005',
    '0008',
    '0009',
    '0010',
    '1010',
    '1011'
];
const icon_history_pad_arr = ['0006', '0007', '0021'];
function iconMap(item) {
    let platform = item.platform;
    if (icon_history_web_arr.indexOf(platform) !== -1) {
        return 'icon_history_web';
    } else if (icon_history_h5_arr.indexOf(platform) !== -1) {
        return 'icon_history_h5';
    } else if (icon_history_pad_arr.indexOf(platform) !== -1) {
        return 'icon_history_pad';
    } else {
        return 'icon_history_client';
    }
}

function groupArr(items) {
    let obj = _.groupBy(items, item => {
        return item.groupByKey;
    });
    let arr = [];
    for (let key in obj) {
        arr.push({
            key,
            value: obj[key]
        });
    }
    return _.sortBy(arr, item => {
        return -item.value[0].date.getTime();
    });
}

function formatPlayHistory(res, params) {
    let items = res.body.PHList;
    let todayTimestamp = res.timeStamp;
    let today = new Date(todayTimestamp);
    let count = res.body.count;
    // 逻辑未知
    let finalObj = {
        isNeedMore: true
    };
    // if (count < params.limit) {
    //     // finalObj.isNeedMore = false;
    // }
    items.forEach(item => {
        let updateTime = item.updateTime;
        let date = new Date(updateTime.replace(/-/g, '/'));
        item.date = date;
        item.groupByKey = formatDate(date, 'YYYY.MM.DD');
        item.platform_icon = iconMap(item);
        
        if (item.videoType  != "LIVE"&&item.totalTime !== undefined && item.currTime !== undefined && (item.totalTime!== 0)) {
            item.playPercent = Math.ceil((item.currTime / item.totalTime) * 100);
            item.playPercentStr = `剩余${100 - item.playPercent}%`;
        }
        else{
            // item.playPercentStr = `剩余100%`;
        }

        item.currTimeStr = formatTime(item.currTime)

        if (isToday(today, date)) {
            item.isToday = true;
            item.groupByKey = '今天';
        } else if (isYesterday(today, date)) {
            item.isYesterday = true;
            item.groupByKey = '昨天';
        } else if (isOffsetDay(today, date, -2)) {
            item.isOffsetDay = true;
            item.groupByKey = '前天';
        }
        item.date = date;
    });

    finalObj.PHList = groupArr(items);
    let arr = _.filter(items, item => {
        return item.programType !== 'OTHER';
    });
    finalObj.filterPHList = groupArr(arr);
    return finalObj;
}

const state = {
    playHistorys: null,
    originPlayHistorysResp: null
};

/**
 * 同步修改
 */
const mutations = {
    setKeyValue(state, { key, value }) {
        state[key] = value;
    }
};

/**
 * 计算属性
 */
const getters = {};

/**
 * 异步请求
 */
const actions = {
    getPlayHistory({ commit }, params) {
        //获取数据
        return fetch(api.PLAY_HISTORY_ACTION, params, userInfo.getHeaderParams()).then(
            res => {
                if (res && res.body && res.body.PHList) {
                    commit('setKeyValue', {
                        key: 'playHistorys',
                        value: formatPlayHistory(res)
                    });
                    commit('setKeyValue', {
                        key: 'originPlayHistorysResp',
                        value: res
                    });
                } else if (res && res.body && Number(res.body.resultCode) === 401) {
                    commit('setKeyValue', {
                        key: 'playHistorys',
                        value: {
                            PHList: [],
                            filterPHList: [],
                            isNotLogin: true
                        }
                    });
                }
            },
            err => {
                console.log(err);
            }
        );
    },
    delPlayHistory({ commit, state }, params) {
        return delet(api.PLAY_HISTORY_ACTION, params, userInfo.getHeaderParams()).then(res => {
            if (res.body && res.body.resultCode === '0000') {
                if(res.body.mId){
                    let mId = res.body.mId;
                    // vue warning mutation clone deep
                    let originPlayHistorysResp = _.cloneDeep(state.originPlayHistorysResp);
                    let PHList = originPlayHistorysResp.body.PHList;
                    let findIndex = null;
                    _.find(PHList, (item, index) => {
                        if (Number(item.mId) === Number(mId)) {
                            findIndex = index;
                            return true;
                        }
                    });
                    if (findIndex != null) {
                        PHList.splice(findIndex, 1);
                        commit('setKeyValue', {
                            key: 'playHistorys',
                            value: formatPlayHistory(originPlayHistorysResp)
                        });
                        commit('setKeyValue', {
                            key: 'originPlayHistorysResp',
                            value: originPlayHistorysResp
                        });
                    }
                }
                else{
                    if(params.cleanUp == 1){
                        commit('setKeyValue', {
                            key: 'playHistorys',
                            value: {
                                PHList: [],
                                filterPHList: []
                            }
                        });

                    }
                }
            }
        });
    }
};

export default {
    namespaced: true,
    state,
    actions,
    getters,
    mutations
};
