function isToday(date1, date2) {
    var year1 = date1.getFullYear();
    var year2 = date2.getFullYear();
    if (year1 !== year2) {
        return false;
    }
    var month1 = date1.getMonth();
    var month2 = date2.getMonth();
    if (month1 !== month2) {
        return false;
    }
    var day1 = date1.getDate();
    var day2 = date2.getDate();
    if (day1 === day2) {
        return true;
    } else {
        return false;
    }
}

function isTomorrow(date1, date2) {
    var year1 = date1.getFullYear();
    var year2 = date2.getFullYear();
    if (year1 !== year2) {
        return false;
    }
    var month1 = date1.getMonth();
    var month2 = date2.getMonth();
    if (month1 !== month2) {
        return false;
    }
    var day1 = date1.getDate();
    var day2 = date2.getDate();
    if (day1 + 1 === day2) {
        return true;
    } else {
        return false;
    }
}

function isYesterday(date1, date2) {
    var year1 = date1.getFullYear();
    var year2 = date2.getFullYear();
    if (year1 !== year2) {
        return false;
    }
    var month1 = date1.getMonth();
    var month2 = date2.getMonth();
    if (month1 !== month2) {
        return false;
    }
    var day1 = date1.getDate();
    var day2 = date2.getDate();
    if (day1 - 1 === day2) {
        return true;
    } else {
        return false;
    }
}

function isOffsetDay(date1, date2, num) {
    var year1 = date1.getFullYear();
    var year2 = date2.getFullYear();
    if (year1 !== year2) {
        return false;
    }
    var month1 = date1.getMonth();
    var month2 = date2.getMonth();
    if (month1 !== month2) {
        return false;
    }
    var day1 = date1.getDate();
    var day2 = date2.getDate();
    if (day1 - num === day2) {
        return true;
    } else {
        return false;
    }
}

function zeroPadding(num) {
    if (num.toString().length < 2) {
        return '0' + num;
    } else {
        return num;
    }
}

function isLeapYear(year) {
    if ((year % 4 === 0 && year % 100 !== 0) || year % 400 === 0) {
        return true;
    } else {
        return false;
    }
}

function singleBlockTime(num) {
    var baseTime = 8;
    var separate = 0.5;
    var calculateNum = baseTime + num * separate;
    var leftPart = Math.floor(calculateNum);
    var rightPart = num % 2 === 0 ? '00' : '30';
    return {
        startNum: leftPart,
        timeString: zeroPadding(leftPart) + ':' + rightPart
    };
}

function get_year_month_str(date) {
    return date.getFullYear() + '_' + zeroPadding(date.getMonth() + 1);
}

function get_year_month_date_str(date) {
    return (
        date.getFullYear() +
        '-' +
        zeroPadding(date.getMonth() + 1) +
        '-' +
        zeroPadding(date.getDate())
    );
}

function month_day(date) {
    return zeroPadding(date.getMonth() + 1) + '.' + zeroPadding(date.getDate());
}

export {
    isToday,
    isTomorrow,
    isYesterday,
    isOffsetDay,
    zeroPadding,
    isLeapYear,
    singleBlockTime,
    get_year_month_str,
    month_day,
    get_year_month_date_str
};
