import { fetch } from 'utils/axios';
import api from "common/api";
const state = {
    isHasData: false, // 判断数据是否回来的
    contentStyle: '',
    actors: '',
    commonInfo: ''
}

/**
 * 同步修改 
 */
const mutations = {
    setHasData(state,data){
        state.isHasData = data
    },
    setActor(state,data){
        state.actors = data
    },
    setContentStyle(state,data){
        state.contentStyle = data
    },
    setCommonInfo(state,data){
        state.commonInfo = data
    }
}

/**
 * 计算属性
 */
const getters = {

}

/**
 * 异步请求
 */
const actions = {
    // 获取节目信息
    async getProgramInfo({commit},cid){
        commit('setActor','')
        commit('setContentStyle','')
        commit('setCommonInfo',{})
        commit('setHasData',false)
        const res = await fetch(api.GET_CONTENT_INFO + cid)
        const {code,body} = res
        if(code == 200){
            const {data: {contentStyle,year,actor}} = body
            commit('setActor',actor)
            commit('setContentStyle',contentStyle)
            commit('setCommonInfo',{year})
            commit('setHasData',true)
        }else{
            console.log('数据获取失败')
        }
    },
}

export default {
    namespaced: true,
    state,
    actions,
    getters,
    mutations
}