<template>
    <div class="txt-img-wrapper">
        <div class="title">{{data.title}}</div>
        <div class="txt-list">
            <a v-for="(item,index) in data.list" :key="index" class="item" @click='goDownload(item)'>
                <span :class="item.picName+' bg'"></span>
                <span class="txt">{{item.text}}</span>
            </a>
        </div>
    </div>
</template>

<style lang="less" scoped>
    @import "../../../css/lib/footerTextImgRow.less";
</style>

<script>
import {commonHandle} from 'common/actionHandle'
import webCommon from 'common/webCommon'
import {mapState, mapGetters, mapActions } from 'vuex';
export default {
    name:"footerTextImgRow",
    props:["data"],
    methods: {

    },
	mounted(){
        console.log(this.data.list,'1111111')
    },
    computed: {
       ...mapState('downloadLink',['action']), 
    },
    methods: {
        
        goDownload(param) {
            // var pageId = webCommon.DOWNLOAD_PAGE_ID;
            // commonHandle(pageId)
            // console.log(param,1111111111111)
            let url = window.location.href;
            if(url.indexOf('download.html') > 0) {
                // location.href=`download.html`
                // console.log(33333333)
                if(param.picName&&(param.picName === 'mobile' || param.picName === 'ipad')) {
                    location.href =`download.html?type=${param.picName}`
                }

            } else {
                if(param.picName&&(param.picName === 'mobile' || param.picName === 'ipad')) {
                    window.open(`download.html?type=${param.picName}`) 
                }
            }
            if(param.picName === 'i_window') {
                // console.log(222222222222)
                commonHandle('downloadUrl',this.action.params.url)
            }
        }
    }
}
</script>