import Vue from 'vue';
import Vuex from 'vuex';

import header from "./modules/header";
import history from "./modules/history";
import userdata from "./modules/userdata";
import programInfo from "./modules/programInfo";
import { fetch } from 'utils/axios';
import LoadManager from 'utils/loadManager';
import api from 'common/api';
import { dataGroupFilter, getGroupRequestList, mergeComp} from './model/indexModel';
import createLogger from '../utils/log/logger';
import webCommon from 'common/webCommon';

Vue.use(Vuex);
const debug = process.env.NODE_ENV !== 'production';

let state = {
    loading: { show: true, img: '' },
    error: { show: false, img: '', message: '出错啦' },
    groups: null,
    showLoginIfr:false,
    headerNavList:[
        {title:"频道", list:["电影","电视剧","综艺","搞笑","体育","动漫","原创"], icon:true,text:true}
    ]
}

let mutations = {
    showError(state, err) {
        if (state.loading.show) state.loading.show = false;
        if (!state.error.show) state.error.show = true;
    },
    showLoading(state, bl) {
        state.loading.show = bl;
    },
    setGroups(state, groups) {
        state.groups = groups;
    },
    setGroupList(state, items) {
        for (let item of items) {
            let index = item.index;
            if (index < 0 || index >= state.groups.length) return;
            let group = state.groups[item.index];
            let comps = group.components;
            let rCopms = null;
            // console.log(item);
            try {
                if (item.data && item.data.code == 200) {
                    rCopms = item.data.body.components;
                    rCopms.forEach((it,index)=>{
                        if(it.data && it.data.length){
                            it.data.forEach((v,i)=>{
                                v.actors = null;
                            })
                        }
                    })
                    
                    // if()
                }
            } catch (e) {}

            if (group) {
                mergeComp(group, rCopms);
            }
        }
    },
    set_key_value(state, { key, value }) {
        state[key] = value;
    },
    setActor( state, param){
        let {actor,obj} = param;
        if(state.groups){
            for(let i =0;i<state.groups.length;i++){
                let group = state.groups[i];
                if(group.id == obj.groupid){
                    for(let j=0; j<group.components.length;j++){
                        let comp = group.components[j];
                        if(comp.id == obj.compid){
                            for (let m=0;m<comp.data.length;m++){
                                let data = comp.data[m];
                                if(data.pID == obj.cid){
                                    data.actors = actor;
                                }
                            }
                        }
                    }
                }
                
            }
        }
        // console.log(actors);
    }
}

let getters = {

}

let actions = {
    pageConfig({ commit }) {
        let pageid = '';
        if(!pageid) pageid = webCommon.HOT_RANK_PAGE_ID;
        const path = api.PAGE_LAYOUT_PATH + pageid; //path+pageid
        fetch(path).then(
            res => {
                const {
                    body: { groups, name }
                } = res;
                commit('showLoading', false);
                let filtergroups = dataGroupFilter(groups);
                commit('setGroups', filtergroups);
                // console.log(filtergroups)
                let requestgroups = getGroupRequestList(filtergroups);
                new LoadManager(requestgroups, function(items) {
                    if (items) commit('setGroupList', items);
                });
                // this.dataGroup = this.dataGroupFilter(groups);
            },
            err => {
                //show error
                console.log(err);
                commit('showError', err);
            }
        );
    },
    getProgramInfo({commit},obj){
       fetch(api.GET_CONTENT_INFO + obj.cid).then(
            res=>{
                const {code,body} = res
                if(code == 200){
                    const {data: {contentStyle,year,actor}} = body
                    commit('setActor',{actor:actor,obj:obj});
                }else{
                    console.log('数据获取失败')
                }
            },
            err=>{
                console.log(err);
            }
       );
    }
}

export default new Vuex.Store({
    state,
    getters,
    mutations,
    actions,
    modules: {
      header,
      history,
      userdata
    },
    strict: debug,
    plugins: debug ? [createLogger()] : []
  }) 