<template>
    <div class="wc100 play-history-wrap rel" v-if="playHistorys">
        <div class="tri"></div>
        <div class="wc100 list-container" v-if="list&&list.length">
            <div class="op-wrap abs pointer" @click="filterShort=!filterShort">
                <span class="icon-switch mr2"  v-bind:class="[filterShort?'active':'']"></span>过滤短视频
            </div>
            <div class="main-container rel">
                <div class="line"></div>
                <template v-for="(item,index) in list">
                    <div class="item cf" v-bind:key="index">
                        <div class="l">

                        </div>
                        <div class="r">
                            <div class="t">
                                {{item.key}}
                            </div>
                            <template v-for="(subitem,index2) in item.value">
                                <div class="sub-item" v-bind:key="index2" @click="playDetail(subitem.contId || subitem.contentId)">
                                    <div class="hover-info cf pdl10">
                                        <div class="wc65 fl ellipsis">
                                            {{subitem.contName}}
                                        </div>
                                        <div class="fr icon-close" @click.stop="handleDelPlayHistory(subitem,index2)">

                                        </div>
                                    </div>
                                    <div class="normal-info cf pdl10">
                                        <div class="wc65 fl ellipsis">
                                            {{subitem.contName}}
                                        </div>
                                        <div class="wc4 fr ml3">
                                            <div class="icon-status" v-bind:class="[subitem.platform_icon]">

                                            </div>
                                        </div>
                                        <div class="mwc24 fr ellipsis c-2nd" v-if="subitem.playPercentStr">
                                            {{subitem.playPercentStr}}
                                        </div>
                                    </div>
                                </div>
                            </template>
                        </div>
                    </div>
                </template>
            </div>
            <div class="btn mt17 pointer" @click="$emit('directPlayCenter')">
                查看更多
            </div>
        </div>
        <div class="empty-container" v-else-if="list&&list.length===0">
            <div class="icon_history_empty">
                
            </div>
            <div class="info alCenter">你暂时还没有观看记录</div>
            <div class="btn mt10 blk pointer" @click="$emit('login')" v-if="playHistorys.isNotLogin">
                登录查看更多
            </div>
        </div>
    </div>
</template>
<script>
import {jumpDeatail} from "common/actionHandle"
export default {
    name:"PlayHistory",
    props: {
        playHistorys: {
            type: Object
        }
    },
    data() {
        return {
            filterShort: false
        };
    },
    computed: {
        list() {
            if (!this.playHistorys) {
                return null;
            }
            if (this.filterShort) {
                return this.playHistorys.filterPHList;
            } else {
                return this.playHistorys.PHList;
            }
        }
    },
    methods: {
        handleDelPlayHistory({ mId, platform }) {
            this.$store.dispatch('history/delPlayHistory', { mId, platform, cleanUp: 0 });
        },
        playDetail(id){
            jumpDeatail({
                cid : id
            });
        }
    }
};
</script>

<style lang="less" scoped>
.main-container {
    max-height: 280px;
    overflow-y: auto;
}
.pdl10 {
    padding-left: 10px;
}
.icon_history_empty {
    width: 126px;
    height: 114px;
    background: url('../../../assets/history/icon_history_empty.png');
    margin-left: auto;
    margin-right: auto;
    margin-top: 80px;
}
.play-history-wrap {
    width: 280px;
    font-size: 12px;
    color: #333333;
    border-top: 4px solid #007aff;
    line-height: 16px;
    .list-container {
        padding-right: 10px;
        padding-left: 15px;
        padding-top: 26px;
        padding-bottom: 16px;
    }
    .empty-container {
        width: 100%;
        height: 336px;
    }
    .tri {
        border-width: 8px;
        border-color: transparent transparent #007aff transparent;
        border-style: solid;
        left: 50%;
        top: -18px;
        margin-left: -8px;
        width: 0;
        height: 0;
        position: absolute;
    }
    .icon-switch {
        width: 12px;
        height: 12px;
        border-radius: 50%;
        border: 1px solid #c6c6c6;
        float: left;
        position: relative;
        top: 2px;
        &.active {
            border: 1px solid #007aff;
            background: #007aff;
        }
    }
    .op-wrap {
        height: 16px;
        vertical-align: middle;
        top: 10px;
        right: 10px;
        z-index: 2;
    }
    .l {
        width: 11px;
        height: 11px;
        float: left;
        background: url('../../../assets/history/icon_history.png');
        position: relative;
        top: 2px;
        z-index: 2;
    }
    .line {
        left: 5px;
        width: 1px;
        height: 100%;
        position: absolute;
        background: #f3f3f3;
    }
    .r {
        margin-left: 1px;
    }
    .t {
        font-size: 14px;
        padding-left: 15px;
        color: #007aff;
    }
    .item {
        margin-top: 10px;
    }
    .sub-item {
        line-height: 20px;
        vertical-align: middle;
        margin-top: 4px;
        margin-left: 5px;
        cursor: pointer;
        .hover-info {
            background: #e9e9e9;
            padding-top: 2px;
            padding-bottom: 2px;
            line-height: 16px;
            display: none;
        }
        &:hover {
            .normal-info {
                display: none;
            }
            .hover-info {
                display: block;
            }
        }
        .wc65 {
            width: 65%;
        }
        .mwc24 {
            max-width: 26%;
        }
        .icon-status {
            margin-left: auto;
            margin-right: auto;
            width: 12px;
            height: 12px;
            top: 4px;
            position: relative;
            margin-right: 4px;
        }
        .icon_history_pad {
            background: url('../../../assets/history/icon_history_pad.png');
        }
        .icon_history_client {
            background: url('../../../assets/history/icon_history_client.png');
        }
        .icon_history_h5 {
            background: url('../../../assets/history/icon_history_h5.png');
        }
        .icon_history_web {
            background: url('../../../assets/history/icon_history_web.png');
        }
        .icon-close {
            width: 12px;
            height: 12px;
            top: 2px;
            position: relative;
            margin-right: 4px;
            background: url('../../../assets/history/icon_del.png');
        }
    }
    .btn {
        border-radius: 12px;
        width: 100px;
        height: 24px;
        line-height: 24px;
        color: #ffffff;
        background: #007aff;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        vertical-align: middle;
    }
}
</style>

