<template>
    <div class="QRCodeWrap">
        <i class="QRCode" v-if="scanData" :style="{backgroundImage : `url(${scanData})`}">
            <div class="QRCodeDecoWrap">
                <div class="QRCodeDeco">
                    <slot>
                    </slot>
                </div>
            </div>
        </i>
        <i v-else class="noneImg">

        </i>
    </div>
</template>
<style scoped lang="less">
    .QRCodeWrap{
        display: block;
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
        position: absolute;
        top : 0;
        left : 0;
        >.QRCode{
            height: 100%;
            width: 100%;
            position: absolute;
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
            left: 0;
            top: 0;
            &:before{
                position: absolute;
                display: block;
                content: "";
                width: 16%;
                height: 16%;
                left : 0;
                top : 0;
                right : 0;
                bottom : 0;
                margin: auto;
                background: url("~@/common/QRCodePayLogo.png") no-repeat center;
                background-size: contain;
                z-index: 2;
                border-radius: 20%;
                background-color: white;
            }
            .QRCodeDecoWrap{
                position: absolute;
                display: table;
                width : 100%;
                height: 100%;
                top : 0;
                left : 0;
                .QRCodeDeco{
                    vertical-align: middle;
                    display: table-cell;
                    max-height: 100%;
                    max-width: 100%;
                }
            }
        }
        >.noneImg{
            background-repeat: no-repeat;
            background-position: center;
            background-image: url("~@/personalCenter/loading.gif");
            background-size: 50% 50%;
            position: absolute;
            height: 100%;
            width: 100%;
            left : 0;
            top : 0;
        }
    }
</style>
<script>
    import QRCode from "qrcode"
    export default {
        props : ["scanUrl","config"],
        data(){
            return {
                scanData : "",
            }
        },
        watch : {
            scanUrl : {
                immediate : true,
                handler(n,o){
                    if(n){
                        QRCode.toDataURL(n,{errorCorrectionLevel :  "M",...this.config}).then((QRCode)=>{
                            this.scanData = QRCode;
                            this.$emit("onQRCodeChange",QRCode);
                        }).catch(err=>{
                            console.log("二维码生成失败",err)
                            this.scanData = "";
                            this.$emit("onQRCodeChange","");
                        })
                    }
                    else{
                        this.$emit("onQRCodeChange","");
                        this.scanData = "";
                    }
                }
            }
        }
    }
</script>