<template>
    <div class="footer">
        <div class="footer-inner">
            <div class="group" v-for="(item,index) in config" :key="index">
                <component :is="item.comStyle" :data="item"></component>
            </div>
            <div class="copyright">
                <div class="txt1"><span>China Mobile Copyright 咪咕视讯科技有限公司 2013—2019</span></div>
                <div class="txt2">
                    <span>沪ICP备15019882号-4</span>                   
                    <a target="_blank" href="http://www.beian.gov.cn/portal/registerSystemInfo?recordcode=31011502003169">
                        <i class="icon_police"></i>
                        沪公网备:31011502003169
                    </a>
                </div>
            </div>
        </div>
    </div>
</template>

<style lang="less">
    @import "../../../css/common/base.less";
    @import "../../../css/group/footer.less";
</style>

<script>
import { footer_config } from "common/webCommon";

import footerTextImgRow from "../lib/footerTextImgRow";
import footerTextCol from "../lib/footerTextCol";
export default {
    name:"wFooter",
    components: {
        footerTextImgRow,
        footerTextCol
    },
    data(){
        return {
            config: footer_config.data
        }
    },
    created(){
        this.config.forEach(item=>{
            item.comStyle = 'footerTextCol';
            if(item.type=='image-text') item.comStyle = 'footerTextImgRow';
        })
    },
    methods: {

    },
	mounted(){
	}
}
</script>