// 版权过滤
const filterCopyRight = (item) =>{
    let nowTime  = new Date().getTime()
    if(item.copyRightVo){
        if(item.copyRightVo.endDate&&(new Date(item.copyRightVo.endDate.replace(/-/g,'/')).getTime()>= nowTime) &&  (item.copyRightVo.terminal.indexOf(1) >-1 || item.copyRightVo.terminal.indexOf(3) >-1) ){
            return true
        }else{
            return false
        }
    }
}
export default filterCopyRight