<template>
    <div class="pModal" :class="type" v-if="value">
        <div class="bg">

        </div>
        <div class="contentWrap">
            <div class="content">
                <div class="close" @click="close">

                </div>
                <div class="detailWrap">
                    <h5 v-if="title">
                        {{title}}
                    </h5>
                    <div class="detail">
                        <div class="detailItem">
                            <slot>

                            </slot>
                        </div>
                    </div>
                    <div class="btnWrap" v-if="type == 'type_1'">
                        <div @click="sure">
                            确认
                        </div>
                        <div  @click="close">
                            取消
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<style scoped lang="less">
    @import "~css/personalCenter";
    .pModal{
        position: fixed;
        height: 100%;
        width : 100%;
        display: table;
        z-index: 5;
        top : 0;
        left : 0;
        .bg{
            position: absolute;
            height: 100%;
            width : 100%;
            top : 0;
            left : 0;
            z-index: 2;
            background-color: rgba(0,0,0,0.3);
        }
        .contentWrap{
            display: table-cell;
            vertical-align: middle;
            position: relative;
            z-index: 4;
            .content{
                background-color: white;
                border: 1px solid #979797;
                border-radius: 8px;
                margin: auto;
                position: relative;
                padding : 0px 10px 15px;
                width : 500px;
                box-sizing: border-box;
                .close{
                    cursor: pointer;
                    position: absolute;
                    top : 12px;
                    right : 10px;
                    width :30px;
                    height : 30px;
                    background-position: center;
                    background-size: contain;
                    background-repeat:  no-repeat;
                    background-image: url("~@/personalCenter/modal_close.png");
                }
                .detailWrap{
                    >h5{
                        height : 54px;
                        line-height: 54px;
                        text-align: left;
                        text-indent: 2em;
                        font-size: 18px;
                        color: #333333;
                        letter-spacing: 0.22px;
                        border-bottom: 1px solid #EEEEEE;
                    }
                }
            }
        }
        /*二级确认框*/
        &.type_1{
            .contentWrap{
                .content{
                    .detailWrap{
                        >h5{
                            text-align: center;
                        }
                        .detail{
                            display: table;
                            height: 150px;
                            width: 100%;
                            .detailItem{
                                vertical-align: middle;
                                display: table-cell;
                            }
                        }
                        .btnWrap{
                            margin-top: 20px;
                            text-align: center;
                            padding-bottom: 30px;
                            >*{
                                display: inline-block;
                                margin: 0 25px;
                                width: 90px;
                                text-align: center;
                                height: 30px;
                                line-height: 30px;
                                color : white;
                                border-radius: 4px;
                                cursor: pointer;
                                &:first-child{
                                    background-color: @mainColor;
                                }
                                &:last-child{
                                    background-color: #999;
                                }
                            }
                        }
                    }
                }
            }
        }
        &.type_2{
            .content{
                padding : 0;
                border-radius: 0;
                width : 800px;
                border: none;
                .close{
                    top: 24px!important;
                    right: 15px!important;
                    width: 30px!important;
                    height: 30px!important;
                }
            }
        }
    }
</style>
<script>
    export default {
        props : ["title","type","value"],
        data(){
            return{
            }
        },
        methods : {
            close(){
                this.$emit("input",false)
            },
            sure(){
                this.$emit("clearAll")
                this.$emit("input",false)
            }
        }

    }
</script>