 * 搜索框弹出的下拉单
*/

<template>
    <div class="search-dropdown">
        <div class="history-result-box" v-show="showHistory">
            <div class="r-title h-r">
                <span class="r-txt-1">历史记录</span>
                <span class="r-txt-2" @mousedown="clear($event)">清除记录</span>
            </div>
            <div class="h-r-content">
                <ul class="h-list">
                    <li class="h-item one-line" v-for="(item,index) in historyResult" :key="index" @mousedown="toSearch($event,item,'history')" >{{item.words}}</li>
                </ul>
            </div>
        </div>
        <div class="search-result-box" v-show="showHot">
            <div class="r-title s-r">
                <span class="r-txt-1">热门搜索</span>
                <span class="r-txt-2" @mousedown="toRank($event)">更多搜索</span>
            </div>
            <div class="s-r-content">
                <ul class="s-r-list">
                    <li class="s-r-item" v-for="(item,index) in hotResult" :key="index" @mousedown="toSearch($event,item,'hot')" >
                        <a class="s-r-item-content">
                            <i :class="['s-r-item-lab',{'color-y1':index==0,'color-y2':index==1,'color-y3':index==2}]" v-if="index<3">{{index+1}}</i>
                            
                            <span class="s-r-item-txt">{{item.name}} </span>
                        </a>
                    </li>
                </ul>
            </div>
        </div>
        <div class="tip-key-box" v-show="searchResult">
            <div class="t-k-content">
                <ul class="t-k-list">
                    <li class="tip-key-item" v-for="(item,index) in searchResult" :key="index" @mousedown="toSearch($event,item,'tip')">
                        {{item}}
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<style lang="less">
    @import "../../../css/group/searchDropDown.less";
</style>

<script>
import {mapState, mapGetters, mapActions } from 'vuex';
import actionHandle, {commonHandle} from 'common/actionHandle';
import webCommon from 'common/webCommon.js';
export default {
    props:["hotResult","historyResult","searchResult"],
    computed: {
        showHistory(){
            let val = (this.historyResult && this.historyResult.length) || null;
            let sval = (this.searchResult && this.searchResult.length) || null;
            if(val && !sval){
                return true;
            }
            return false;
        },
        showHot() {
            let val = (this.hotResult && this.hotResult.length) || null;
            let sval = (this.searchResult && this.searchResult.length) || null;
            if(val && !sval){
                return true;
            }
            return false;
        }
    },
	mounted(){
    },
    created(){
        // console.log(this.hotResult);
        // console.log(this.historyResult);

    },
    methods: {
        toSearch(event,item,sign) {
            event.preventDefault()
            var val;
            if(sign === 'history') {
                val = item.words;
            }
            if(sign === 'hot') {
                val = item.name;
            }
            
            if(sign === 'tip') {
                val = item;
            }
            // console.log(val,'val')
            window.sessionStorage.setItem('keywords',val);
            this.$store.commit('header/setHistoryResult',val);
            this.$store.commit('header/toSearch',val);
        },
        // 清除搜索历史
        clear(event) {
            event.preventDefault()
            // console.log('====================================');
            // console.log(1111);
            // console.log('====================================');
            localStorage.clear();
            this.$store.commit('header/clearHistoryResult');
        },
        // 更多搜索，跳转排行页
        toRank(event) {
            event.preventDefault()
            commonHandle(webCommon.HOT_RANK_PAGE_ID)
        }
    }
}
</script>
