import promise from 'es6-promise';
promise.polyfill();
import Vue from "vue";
import store from "store/rankStore";
import channelLibrary from "components/rank";
import { post, fetch, patch, put, delet } from "utils/axios";
//定义全局变量
Vue.prototype.$post = post;
Vue.prototype.$fetch = fetch;
Vue.prototype.$patch = patch;
Vue.prototype.$put = put;
Vue.prototype.$delet = delet;


export default class RankView {
  constructor() {
    this.options = {
      el: "",
      v_version: ""
    };
  }

  init(data) {
    //初始化Render
    this.options = data || this.options;
    var vindex = new Vue({
      el: this.options.el,
      store,
      render: h => h(channelLibrary)
    });
  }
}

let rankView = new RankView();
rankView.init({ el: "#app" });
