// import $ from "jquery"
export default function (param) {
    return new Toast(param);
}

class Toast{
    constructor(param  = {}){
        this._initParams(param)._initEvent();
    }
    _initParams(param){
        if(typeof param == "string"){
            this.content = param;
            this.time = 2000;
            this.type = "type_1";
        }else{
            this.type = param.type || "type_1";
            this.time = param.time || 2000;
            this.content = param.content;
        }

        return this;
    }
    _initEvent(){
        this._createEle().show();
    }
    _createEle(){
        let wrap =  document.createElement("div");
        wrap.className = "pToast"
        wrap.innerHTML = this.content;
        this._ele = wrap;
        return this;
    }
    show(){
        document.body.appendChild(this._ele)
        this.timeout = setTimeout(()=>{
            this.close();
        },this.time)
    }
    close(){
        clearTimeout(this.timeout)
        this._ele.parentNode.removeChild(this._ele)
    }
}