import KS from "KS";
import userInfo from "userInfo";

// 科升埋点(针对www站的)
class KSStatistics{
    constructor(){
        this._initParam()._initKS().setUserInfoParam();
    }
    _initParam(){
        Object.assign(this,{
            awaitArray : [],
            isReady : false,
            KSInstance : "",
        })
        return this;
    }
    _initKS(){
        this.KSInstance = new KS();
        return this;
    }
    // ***** 这个方式是必须调用的，否则埋点任务只会添加在队列中，不会执行*****
    ready(){
        if(!this.isReady){
            this.isReady = true;
            this.awaitArrayExecute();
        }

        return this;
    }
    awaitArrayExecute(){
        if(this.awaitArray.length){
            for(let i = 0 ; i < this.awaitArray.length; i ++){
                this.awaitArray[i]();
                i--;
                this.awaitArray.splice(i,1);
            }
        }
        
        return this;
    }
    checkStatus(cb){
        if(this.isReady){
            cb&&cb();
        }
        else{
            this.awaitArray.push(cb);
        }
        
        return this;
    }
    PV(param){
        this.checkStatus(()=>{
            this.KSInstance.PV(param);
        });
        
        return this;
    }
    setStaticParam(param){
        this.KSInstance.setInstanceConstParam(param)
        return this;
    }
    setUserInfoParam(){
        let userDetail = userInfo.getUserInfo();
        let userId = "",account = "";
        if(userInfo.isLogin()){
            userId = userDetail.userId
            account = userDetail.mobile
        }

        this.setStaticParam({
            userId,
            account
        });
        
        return this;
    }
}

export default new KSStatistics();