(function(global, factory) {
    if (typeof module === 'object' && module.exports) {
        module.exports = factory(global);
    } else if (typeof define === 'function' && define.amd) {
        define(function() {
            return factory(global);
        });
    } else {
        global.UserInfo = factory(global);
    }
})(typeof window !== 'undefined' ? window : this, function(window) {
    //import axios from "axios";

    // var apis = "/api";//
    var apis = "/gateway"; ///gateway
    // document.domain = "miguvideo.com";

    var HOME_PAGE = "/mgs/msite/prd/index.html";
    var USERINFO_URL = apis + "/user/v1/user-info/"; //用户信息获取地址
    var CHECK_4G_URL = apis + "/user/v1/user-infos?userIds=";
    var MGCN_LOGIN_URL = "https://passport.migu.cn/login?sourceid=203021&apptype=2&authType=&display=&relayState=login"; //一级登录地址
    var AUTO_4G_PARAMS = "&forceAuthn=false&isPassive=true"; //4G自动登录
    var NO_AUTO_PARAMS = "&forceAuthn=true&isPassive=false"; //普通登录

    //预览环境
    var MGCN_LOGOUT_URL = "https://passport.migu.cn/logout?sourceid=203021&callbackURL=&relayState=";
    var MEMBERINFO_URL = apis + "/user/v1/member-info/"; //会员信息获取地址
    var COMMON_MIGU_LOGIN = "https://m.miguvideo.com/mgs/login/migu_login_callback.htm?&relayState=login"; //common下的登录页

    
    var UserInfo = function() {
        this.initUserData();
        this._init();
    };

    UserInfo.prototype = {
        _init: function() {
            this._checkUserInfo();
            this._ssoLogin();
            // if (!this.info.isLogin) {
            //     this._autoLogin();
            // }
        },
        /**
         * 获取登录用户信息
         */
        getUserInfo: function() {
            return this.info.userInfo;
        },
        /**
         * 获取头部参数
         */
        getHeaderParams: function() {
            return this.info.userHeaderInfo;
        },
        /**
         * 是否用户已经登录
         */
        isLogin: function() {
            return this.info.isLogin;
            //return true;
        },
        /**
         * 登录
         * @param {跳转地址} href
         */
        login: function(href) {
            if (this.info.userInfo) {
                return this.info.userInfo;
            }
            //判断是否预览环境登录
            openWindow(encodeURI(MGCN_LOGIN_URL + NO_AUTO_PARAMS + "&callbackURL=" + (href || location.href)));
        },
        /**
         * 登出
         */
        logout: function(url,cb) {
            try {
                if (window.sessionStorage && sessionStorage.getItem('memberInfo')) {
                    window.sessionStorage.removeItem('memberInfo');
                }
                this.info.isLogin = false;
                var lImg = document.createElement('img');
                var self = this;
                lImg.onload = lImg.onerror = function() {
                    lImg.onload = lImg.onerror = null;
                    lImg = null;
                    Cookies.removeItem('userInfo');
                    self.initUserData();
                    openWindow(url ? url : HOME_PAGE);
                    cb&&cb();
                };
                lImg.src = MGCN_LOGOUT_URL;
            } catch (err) {
                Cookies.removeItem('userInfo');
                openWindow(url ? url : HOME_PAGE);
                cb&&cb()
            }
        },
        initUserData : function(url) {
            this.info = {
                userInfo: null,
                isLogin: false,
                is4g: false,
                userHeaderInfo: null,
                memberInfo: null,
                extendInfo: null
            };
        },
        /**
         * 获取会员信息
         * @param {回调函数} cb
         */
        getMemberInfo: function(cb, needFetch) {
            var self = this;
            var selfNeedFetch = needFetch !== false;

            //check isLogin
            if (!self.info.isLogin) {
                cb(null);
                return;
            }
            
            //get sessioin
            if (window.sessionStorage && sessionStorage.getItem("memberInfo") && selfNeedFetch) {
                cb(JSON.parse(sessionStorage.getItem("memberInfo")));
                return;
            }
            // 
            if(!selfNeedFetch){
                window.sessionStorage&&window.sessionStorage.removeItem("memberInfo")
            }

            /** trial 体验会员
             * diamond 钻石会员(原普通会员, normal)
             * golden 黄金会员(原v + 会员, vplus)
             * black_diamond 钻石(tv尊享) 会员
             * province_biz_gansu 甘肃分省会员
             * province_biz_guangdong 广东分省会员
             * v15 v15会员
             * migu_dazhanbao 大站包会员
             * tv_family 家庭tv产品会员
             */
            //get network
            userAjax(
                {
                    method: 'get',
                    url: MEMBERINFO_URL,
                    headers: self.info.userHeaderInfo
                },
                function(rep) {
                    console.log(rep);
                    if (rep && rep.code == 200 && rep['body'].resultCode == 'SUCCESS') {
                        self.info.memberInfo = rep['body'];
                        if (window.sessionStorage) {
                            sessionStorage.setItem(
                                'memberInfo',
                                JSON.stringify(self.info.memberInfo)
                            );
                        }
                        cb(self.info.memberInfo);
                        return;
                    } else {
                        cb(null);
                        return;
                    }
                }
            );
        },
        /**
         * App端内登录
         * @param {*} cb 回调函数 返回userInfo
         * ex :window.mgUserInfo.getAppInfo(function(uinfo){console.log(uinfo)});
         */
        getAppInfo: function(cb) {
            var self = this;
            if (!this._checkAppLogined()) {
                cb(null);
                return;
            }
            var userInfoArr = Cookies.getItem('UserInfo').split('|');
            //判断userinfo和usertoken是否一致 不一致删除userinfo
            if (Cookies.getItem('userInfo')) {
                var uinfo = JSON.parse(decodeURIComponent(Cookies.getItem('userInfo')));
                if (uinfo['userId'] != userInfoArr[0] || uinfo['userToken'] != userInfoArr[1]) {
                    Cookies.remove('userInfo');
                }
            }
            this._checkUserInfo();
            if (this.info.userInfo) {
                cb(this.info.userInfo);
                return;
            }
            userAjax(
                {
                    method: 'get',
                    url: USERINFO_URL,
                    headers: {
                        channel: 'H5',
                        userId: userInfoArr[0],
                        userToken: userInfoArr[1],
                        clientId: Cookies.getItem('ClientId'),
                        SDKCEId: Cookies.getItem('ClientId')
                    }
                },
                function(rep) {
                    if (!rep || !rep.data || !rep.data.body || !rep.data.body.data) {
                        cb(null);
                        return;
                    }
                    var userInfoObj = rep.data.body.data;
                    self.info.userInfo = {
                        userId: userInfoArr[0],
                        userToken: userInfoArr[1],
                        clientId: Cookies.getItem('ClientId'),
                        sname: userInfoObj.sname ? userInfoObj.sname : '',
                        mobile: userInfoObj.mobile,
                        picture: userInfoObj.picture ? userInfoObj.picture : '',
                        sex: userInfoObj.sex ? userInfoObj.sex : ''
                    };
                    Cookies.setItem('userInfo', JSON.stringify(self.info.userInfo), {
                        expires: 7
                    });
                    self._setHeaderInfo();
                    cb(self.info.userInfo);
                }
            );
        },
        /**
         * 通过jsBridge设置userInfo
         * @param {*} obj
         */
        setJsBridgeUserInfo: function(obj) {
            try {
                if (obj && Cookies.getItem('ClientId')) {
                    self.info.userInfo = {
                        userId: obj.uid ? obj.uid : '',
                        userToken: obj.usertoken ? obj.usertoken : '',
                        clientId: Cookies.getItem('ClientId'),
                        sname: obj.uname ? obj.uname : '',
                        mobile: obj.mobile ? obj.mobile : '',
                        picture: obj.userhead ? obj.userhead : '',
                        sex: '',
                        chinaMoblie: obj.chinaMoblie ? obj.chinaMoblie : ''
                    };
                    Cookies.setItem('userInfo', JSON.stringify(self.info.userInfo), {
                        expires: 7
                    });
                    self._setHeaderInfo();
                }
            } catch (err) {
                console.error(err);
            }
        },
        /**
         * 检查userinfo是否在cookies并插入info里
         */
        _checkUserInfo: function() {
            try {
                if (Cookies.getItem('userInfo')) {
                    this.info.userInfo = JSON.parse(
                        decodeURIComponent(Cookies.getItem('userInfo'))
                    );
                    this._setHeaderInfo();
                }
            } catch (err) {
                console.error(err);
            }
        },
        //单点登录
        _ssoLogin: function() {
            if (getUrlParam("login") != "SSO") return;

            var ssotoken = getUrlParam("token") || null; //获取一级token
            var loginerr = getUrlParam("loginerr") || 0; //登录次数
            var typeToken = getUrlParam("tokentype") || "";

            if (ssotoken) {
                typeToken += ssotoken; //合并一级token
            } else {
                //不存在认为是登出状态
                this.logout();
                return;
            }
            //本地存储和获取不对等 重新获取用户信息
            if ((!this.info.userInfo || (typeToken != this.info.userInfo.ssotoken)) && !loginerr) {
                openWindow(COMMON_MIGU_LOGIN + "&callbackURL=" + encodeURIComponent(location.href + "&loginerr=1") + "&token=" + typeToken);
            }
        },
        //插入头部
        _setHeaderInfo: function() {
            if (this.info.userInfo) {
                this.info.userHeaderInfo = {
                    'Content-Type': 'application/json',
                    channel: 'H5',
                    userId: this.info.userInfo.userId,
                    userToken: this.info.userInfo.userToken,
                    clientId: this.info.userInfo.clientId,
                    SDKCEId: this.info.userInfo.clientId,
                    clientCityId: this.info.userInfo.clientCityId,
                    clientProvinceCode: this.info.userInfo.clientProvinceCode
                };
                this.info.isLogin = true;
            }
        },
        //4g下自动登录 且24小时之内只check一次
        _autoLogin: function() {
            // if (Cookies.getItem("check4g") && Cookies.getItem("check4g") == "1") return;
            // userAjax(
            //     {
            //         method: "get",
            //         url: CHECK_4G_URL
            //     },
            //     function(rep) {
            //         if (rep && rep["x-up-bear-type"] == "LTE") {
            //             Cookies.setItem("check4g", "1", {
            //                 expires: 1
            //             });
            if(getUrlParam('loginerr') != '1') {
                var loginerr = location.href.indexOf('?') == -1 ? '?&loginerr=1' : '&loginerr=1';
                location.href = 'http://passport.migu.cn/login?sourceid=203021&apptype=2&authType=&display=&relayState=login' + AUTO_4G_PARAMS + "&callbackURL=" + encodeURIComponent(location.href + loginerr);
            }
            //         }
            //     }
            // );
        },
        /**
         * 判断是否在app登录 如果登出删除userinfo
         */
        _checkAppLogined: function() {
            //符合条件
            var flag = true;

            if (
                (getUrlParam("platform") != "ios" || getUrlParam("platform") != "android") &&
                (!Cookies.getItem("UserInfo") || !Cookies.getItem("ClientId"))
            ) {
                flag = false;
            }
            if (Cookies.getItem("UserInfo")) {
                var userInfoArr = Cookies.getItem("UserInfo").split("|");
                if (userInfoArr[0] == "" || userInfoArr[0] == undefined || userInfoArr[1] == "" || userInfoArr[1] == undefined) {
                    flag = false;
                }
            }
            if (!flag) {
                if (Cookies.getItem('userInfo')) {
                    Cookies.removeItem('userInfo');
                }
            }
            return flag;
        },
        /**
         * 编写扩展字段 (覆盖原来)
         * @param {内容}} str
         */
        writeExInfo: function(str) {
            this.info.extendInfo = str;
            Cookies.setItem("extendInfo", str);
        }
    };

    /**
     * cookies
     */
    var Cookies = {
        setItem: function(name, value, expires) {
            var text = encodeURIComponent(value),
                MILLISECONDS_OF_DAY = 24 * 60 * 60 * 1000;
            if (typeof expires === "number") {
                var date = new Date();
                date.setTime(date.getTime() + expires * MILLISECONDS_OF_DAY);
                if (expires == -1) {
                    text += "; expires=-1";
                } else {
                    text += "; expires=" + date.toUTCString();
                }
            }
            text += "; path=/; domain=.miguvideo.com";
            document.cookie = name + "=" + text;
        },
        getItem: function(name) {
            var arr = document.cookie.match(new RegExp("(^| )" + name + "=([^;]*)(;|$)"));
            if (arr !== null) {
                return decodeURIComponent(arr[2]);
            }
            return null;
        },
        removeItem: function(name) {
            this.setItem(name, "", -1);
        }
    };

    /**
     * 咪咕ajax
     * @param {类似jquery参数} pararm
     * @param {*} cb
     */
    var mgAjax = function(pararm, cb) {
        var data = pararm["data"] ? pararm["data"] : null;
        var method = pararm["method"] ? pararm["method"] : "get";
        var murl = pararm["url"] ? pararm["url"] : "";
        var mheader = pararm["headers"] ? pararm["headers"] : null;
        var getParams = pararm["params"] ? pararm["params"] : null;

        function objTostr(objs) {
            var dataStr = "";
            for (var ida in objs) {
                if (dataStr != "") {
                    dataStr += "&";
                }
                dataStr += ida + "=" + objs[ida];
            }
            return dataStr;
        }
        //get添加参数
        if (method == "get" && getParams) {
            murl = addParameterToURL(murl, objTostr(getParams));
            console.log("url ==>", murl);
        }
        //如果没有url返回null
        if (!murl || murl == "" || murl == undefined) {
            cb(null);
            return;
        }

        var xhr = (function() {
            if (window.XMLHttpRequest) {
                //主流浏览器提供了XMLHttpRequest对象
                return new XMLHttpRequest();
            } else if (window.ActiveXObject) {
                //低版本的IE浏览器没有提供XMLHttpRequest对象
                //所以必须使用IE浏览器的特定实现ActiveXObject
                return new ActiveXObject("Microsoft.XMLHttpRequest");
            }
        })();

        xhr.onreadystatechange = function() {
            if (xhr.readyState === 4) {
                if (xhr.status === 200) {
                    //获取成功后执行操作
                    //数据在xhr.responseText
                    var resJson = JSON.parse(xhr.responseText);
                    if (resJson && xhr.getResponseHeader("x-up-bear-type")) {
                        resJson["x-up-bear-type"] = xhr.getResponseHeader("x-up-bear-type");
                    }
                    cb(resJson);
                    return;
                } else {
                    cb(null);
                }
            }
        };

        xhr.open(method, murl, true);
        for (var attr in mheader) {
            xhr.setRequestHeader(attr, mheader[attr]);
        }
        var dataStr = "";
        if (data) {
            dataStr = objTostr(data);
        } else {
            dataStr = null;
        }

        xhr.send(dataStr);
    };

    /**
     * 自适应 $.ajax 或者 mgAjax
     * @param {类似jquery参数} param
     * @param {*} cb
     */
    var userAjax = function(param, cb) {
        if (window.$ && typeof $.ajax !== "undefined") {
            param["success"] = function(rep, status, xhr) {
                if (rep && xhr.getResponseHeader("x-up-bear-type")) {
                    rep["x-up-bear-type"] = xhr.getResponseHeader("x-up-bear-type");
                }
                cb(rep);
            };
            param["error"] = function(err) {
                cb(null);
            };
            $.ajax(param);
        } else {
            mgAjax(param, function(res) {
                cb(res);
                return;
            });
        }
        return null;
    };

    /**
     * 获取当前url
     * @param {*} name
     */
    var getUrlParam = function(name) {
        var reg = new RegExp("(^|\\?|&)" + name + "=([^&]*)(\\s|&|$)", "i");
        if (reg.test(window.location.search)) {
            return decodeURIComponent(RegExp.$2.replace(/\+/g, " "));
        }
        return null;
    };

    /**
     * 打开窗口
     * @param {*} url 
     * @param {*} param 
     */
    var openWindow = function(url) {
        if(isIframe){
            window.parent.postMessage(JSON.stringify({"type":"login"}),"*");
        }else {
            location.href = url;
        }
    };
    //追加url
    var addParameterToURL = function(url, param) {
        var _url = url;
        _url += (_url.split("?")[1] ? "&" : "?") + param;
        return _url;
    };
    //isIframe
    var isIframe = getUrlParam("isIframe") || null;

    var mgUserInfo = null;
    
    if (!window.mgUserInfo) {
        mgUserInfo = window.mgUserInfo = new UserInfo();
    } else {
        mgUserInfo = window.mgUserInfo;
    }
    return mgUserInfo;
});
