<template>
    <div class="wc100 login-info-box rel" v-if="userInfo">
        <div class="tri"></div>
        <div class="main-container">
            <div class="user-info cf">
                <div class="l">
                    <img :src="userInfo.picture||defaultUserPhoto" alt="" v-if="userInfo"/>
                </div>
                <div class="r">
                    <div class="info cf">
                        <div class="nick-name fl">
                           <span v-if="userInfo">{{userInfo.sname}}</span>
                        </div>
                        <div class="op-btn fr">
                            <span class="mr16 pointer" @click.stop="handleLogin">切换</span>
                            <span class="mr16 pointer" @click.stop="handleLogout">退出</span>
                        </div>
                    </div>
                    <div class="info">
                        {{memberDetailInfo&&memberDetailInfo.title || ''}} 
                    </div>
                </div>
            </div>
            <div class="b-container">
                <div class="line"></div>
                <div v-if="memberDetailInfo.expires" class="detail-info mt10">有效期至：{{memberDetailInfo.expires}}</div>
                <div class="bottom-info cf">
                    <div class="fl reorder pointer" @click.stop="openCommonMember" v-if="isNormalMemeber==true">续费</div>
                    <div class="fl reorder pointer" @click.stop="openCommonMember"  v-if="isNormalMemeber==false">开通会员</div>
                    <div class="fr openmore pointer" @click.stop="openSportMember">开通体育特权</div>
                    <!-- <div class="btn-group cf">
                        <div class="reorder fl pointer">
                            <span v-if="isNormalMemeber===true">续费</span>
                            <span v-else-if="isNormalMemeber===false">开通会员</span>
                        </div>
                        <div class="openmore fl">开通体育特权</div>
                    </div> -->
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState } from 'vuex';
import defaultUserPhoto from '@/defaultUserPhoto.png';
import { formatDate } from 'utils/format';
import memberPackageAction  from "memberPackageAction";
import {personalCenterRouters} from "common/enum"

export default {
    name:"LoginBox",
    props: {
        "userData" : {
            type : Object,
            default : ''
        }
    },
    data() {
        return {
            defaultUserPhoto,
        };
    },
    mounted() {
        if (this.isLogin) {
            this.getMemberInfo();
        }
    },
    computed: {
        ...mapState({
            memberInfo: state => state.userdata.memberInfo
        }),
        // 是否登录
        isLogin() {
            return this.userData.isLogin;
        },
        // 用户信息
        userInfo() {
            if (!this.isLogin) {
                return null;
            }
            return this.userData && this.userData.userInfo
                ? this.userData.userInfo
                : null;
        },
        // 是否是 普通权益的会员
        isNormalMemeber() {
            if (!this.memberDetailInfo) {
                return false;
            } else {
                return true;
            }
        },
        // 用户普通会员详情 钻石 黄金会员 etc
        memberDetailInfo() {
            let _res = memberPackageAction.getMemberDetail(this.memberInfo);
            return _res&&(_res.commonMember ||  (_res.sportMember&&_res.sportMember[0]));
        }
    },
    methods: {
        openSportMember(){
            this.$emit('openSportMember')
        },
//       开通普通会员
        openCommonMember(){
            location.href = "personalCenter.html#/memberCenter"
        },
        //获取会员信息
        getMemberInfo() {
            this.$store.dispatch("userdata/getMemberInfo");
        },
        // 登录
        handleLogin() {
            this.$emit("switchLogin");
        },
        // 登出
        handleLogout() {
            this.$store.commit("userdata/logout");
//            location.reload()
        }
    }
};
</script>

<style lang="less" scoped>
.mr16 {
    margin-right: 16px;
}
.mt5 {
    margin-top: 5px;
}
.icon-status {
    width: 12px;
    height: 12px;
    float: left;
}
.login-info-box {
    font-size: 12px;
    color: #323232;
    width: 300px;
    border-top: 4px solid #007aff;
    line-height: 16px;
    vertical-align: middle;
    .reorder {
        background-image: linear-gradient(-45deg, #958468 0%, #dcd2aa 100%);
        border-radius: 10px;
        padding-left: 14px;
        padding-right: 14px;
        height: 20px;
        color: #ffffff;
        text-align: center;
        margin-right: 8px;
        background-color: #958468;
    }
    .openmore {
        border: 1px solid #c6bd85;
        color: #c6bd85;
        border-radius: 10px;
        width: 100px;
        height: 18px;
        line-height: 18px;
        text-align: center;
        margin-right: 10px;
    }
    .bottom-info {
        padding-top: 12px;
        padding-bottom: 12px;
        height: 20px;
        line-height: 20px;
        vertical-align: middle;
        color: #b3b3b3;
        // padding-left: 10px;
        position: relative;
    }
    .btn-group {
        position: absolute;
        top: 13px;
        right: 0;
    }
    .main-container {
        padding-left: 10px;
        padding-right: 10px;
        padding-top: 15px;
        padding-bottom: 15px;
    }
    .line {
        width: 100%;
        height: 1px;
        background: #e6e6e6;
    }
    .op-btn {
        color: #9a9a9a;
    }
    .info {
        height: 16px;
    }
    .user-info {
        padding-bottom: 15px;
        .l {
            width: 30px;
            height: 30px;
            border-radius: 50%;
            overflow: hidden;
            float: left;
            margin-left: 10px;
            img {
                width: 100%;
                height: auto;
                border-radius: 50%;
            }
        }
        .r {
            margin-left: 50px;
        }
    }
}
.tri {
    border-width: 8px;
    border-color: transparent transparent #007aff transparent;
    border-style: solid;
    top: -18px;
    right: 24px;
    width: 0;
    height: 0;
    position: absolute;
}
</style>

