<template>
    <div class="openMemberModalToPayWrap">
        <div class="" v-if="isPay">
            <h5>
                开通方式
            </h5>
            <div class="payActionWrap">
                <div class="left">
                    <tab class="typeDetailWrap" type="type_3" :list="list"  idKey="id"  :value="value"
                         @input="input">
                        <div slot-scope="{data}" class="slotDetail" :class="{active : data.id == value}">
                            <i :class="data.class">

                            </i>
                            <div class="title">
                                {{data.title}}
                            </div>
                        </div>
                    </tab>
                </div>
                <div class="right">
                    <to-pay :isContinuous="selectItem.isContinuous" v-if="selectItem" :info="info" :payUrl="payUrl" :scanUrl="QRCode" :type="selectItem.class"></to-pay>
                </div>
            </div>
        </div>
        <div v-else class="adv" :style="{backgroundImage : imgUrl?`url(${imgUrl})`:''}" @click="$emit('advJump')">
        </div>
    </div>
</template>
<style lang="less" scoped>
    .openMemberModalToPayWrap{
        padding : 20px 0;
        width: 760px;
        margin: auto;
        height: 368px;
        box-sizing: border-box;
        .adv{
            cursor: pointer;
            height: 100%;
            position: relative;
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
        }
        h5{
            font-size: 16px;
            color: #4D4D4D;
            font-weight: bold;
        }
        .payActionWrap{
            box-shadow: 0 1px 8px 0 rgba(0,0,0,0.10);
            margin-top: 15px;
            background: #F8F7F7;
            &:after{
                clear: both;
                display: block;
                content: '';
            }
            .left{
                float: left;
                width: 160px;
            }
            .right{
                float: right;
                width: 600px;
                background: white;
            }
            .slotDetail{
                position: relative;
                >*{
                    display: inline-block;
                    vertical-align: middle;
                }
                i{
                    width: 20px;
                    height: 20px;
                    background-position: center;
                    background-size: contain;
                    background-repeat: no-repeat;
                    &.QRCode,&.fixQRCode{
                        background-image: url("~@/openMemberModal/aggregatePay.png");
                    }
                    &.HF{
                        background-image: url("~@/openMemberModal/hf.png");
                    }
                }
                .title{
                    font-size: 16px;
                    color: #4D4D4D;
                    margin-left: 5px;
                }
            }
        }
    }
</style>
<script>
    import tab from "components/openMemberModal/common/tab"
    import toPay from "components/openMemberModal/children/memberPayDetail"
    export  default {
        props : ["value", "list","payUrl","QRCode","isPay","imgUrl","info"],
        data(){
            return {
                selectItem : ""
            }
        },
        components : {
            tab,
            toPay
        },
        methods : {
            input(val){
                this.$emit("input",val);
                this.selectItem = this.findItemByValue(val);
                console.log(this.selectItem)
            },
            findItemByValue(val){
                for(let i = 0 ; i < this.list.length ; i ++){
                    if(val === this.list[i].id){
                        return this.list[i];
                    }
                }

                return null;
            }
        }
    }
</script>