<template>
    <div class="openMemberModalUserInfo" >
        <div class="imgWrap">
            <div class="img" :style="{backgroundImage : headImg?`url(${headImg})`: ''}">

            </div>
        </div>
        <div class="info">
            <h5 class="name">
                {{userInfo&&userInfo.sname || '咪咕用户'}}
            </h5>
            <p class="memberInfo">
               {{ memberDetail?memberDetail.title:'普通用户'}}
            </p>
        </div>
    </div>
</template>
<style scoped lang="less">
    @import "~css/personalCenter";
    .openMemberModalUserInfo{
        height: 80px;
        box-sizing: border-box;
        display: table;
        padding : 0 60px 0 25px;
        max-width: 100%;
        width : 100%;
        >*{
            display: table-cell;
            vertical-align: middle;
        }
        .imgWrap{
            width : 50px;
            .img{
                height: 50px;
                background-size: cover;
                background-position: center;
                background-repeat: no-repeat;
                border-radius: 50%;
                background-image: url("~@/defaultUserPhoto.png");
            }
        }
        .info{
            padding-left: 18px;
            .name{
                font-size: 16px;
                color: #C19E00;
                margin-bottom: 3px;
            }
            .memberInfo{
                font-size: 14px;
                color: #CCCCCC;
            }
        }
    }
</style>
<script>
    import {mapState,mapMutations,mapActions}  from "vuex"
    import PModal from "components/personalCenter/common/PModal"
    import memberPackageAction from "memberPackageAction"
    export default {
        props : ["status","type","value"],
        data(){
            return{
                memberName : "",
            }
        },
        computed:{
            ...mapState("userdata/",["isLogin","userInfo","userHeaderInfo","memberInfo"]),
            headImg(){
                return this.isLogin&&this.userInfo&&this.userInfo.picture;
            },
            // 用户普通会员详情 钻石 黄金会员 etc
            memberDetail() {
                let _res = memberPackageAction.getMemberDetail(this.memberInfo);
                return _res&&(_res.commonMember ||  (_res.sportMember&&_res.sportMember[0]));
            }
        },
        components : {
            PModal
        },
        mounted(){
        },
        methods : {
            input(value){
                this.$emit("input",value);
            },
            sure(){
            }
        }

    }
</script>