<template>
    <div class="login-wrapper">
        <div class="login-iframe">
            <div class="login-close-btn" @click="close()"></div>
            <iframe id="login_frame" height="100%" scrolling="no" width="460px" frameborder="0" 
            :src="getSrc()">

            </iframe>
        </div>
    </div>
</template>

<style lang="less" scoped>

    .login-wrapper {
        position: fixed;
        left: 0px;
        top: 0px;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.5);
        z-index: 4860; 

        
        .login-close-btn {
            &::before {
                content:"\2716";
                width: 30px;
                height: 30px;
                font-size: 26px;
                color: #f39610;
            }
            right:0;
            position: absolute;
            margin-top: 5px;
            margin-right: 10px;
            cursor: pointer;
        }
        .login-iframe {
            width: 460px; 
            height: 420px; 
            overflow: hidden; 
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);
            -moz-transform: translate(-50%, -50%);
            -webkit-transform: translate(-50%, -50%);
            -o-transform: translate(-50%, -50%); 	
        }
    }
    
</style>

<script>
import utils from "@@/utils/utils"
export default {
    name:"login-iframe",
    mounted(){
       
    },
    methods: {
        getSrc() { //location.origin +
        let callbackURL = utils.addParameterToURL(location.href,"isIframe=web");
        return "https://passport.migu.cn/login?sourceid=203021&apptype=2&forceAuthn=true&isPassive=false&authType=&display=&nodeId=70027513&relayState=login&weibo=1"+
                "&callbackURL="+encodeURIComponent(callbackURL);// 203009
            
            // return "https://passport.migu.cn/login?sourceid=203021&apptype=2&authType=&display=&relayState=login&forceAuthn=true&isPassive=false"
        },
        close() {
            this.$emit("closeLogin");
        }
    },
	mounted(){
	}
}
</script>