<template>
    <p-modal :value="value" @input="input" type="type_2">
        <open-member-modal-header :status="value"></open-member-modal-header>
        <div class="wrap" v-if="isLogin">
            <member-type-tab v-model="memberType" class="memberType" :list="memberList" @input="typeTabChange"></member-type-tab>
            <member-type-tab-detail v-model="goodsType" class="goodsType"  :list="goodsList"  @input="typeTabDetailChange"></member-type-tab-detail>
            <member-open v-model="payType" :payUrl="payUrl" :QRCode="QRCode" :isPay="isPay" :imgUrl="imgUrl" class="payType"
                         :info="getPayTextInfo"
                         :list="goodsPayTypeList"
                         @advJump="advJump"
                         @input="payTypeChange"></member-open>
        </div>
        <not-login v-else></not-login>
    </p-modal>
</template>
<style scoped lang="less">
    @import "~css/personalCenter";
</style>
<script>
    import PModal from "components/personalCenter/common/PModal"
    import openMemberModalHeader from "components/openMemberModal/children/header"
    import memberTypeTab from "components/openMemberModal/children/memberTypeTab"
    import memberTypeTabDetail from "components/openMemberModal/children/memberTypeTabDetail"
    import memberOpen from "components/openMemberModal/children/memberPayTab"
    import notLogin from "components/openMemberModal/common/notLogin"

    import openMemberCommon  from "memberPackageAction"
    import api  from "api"
    import {mapState,mapMutations,mapActions}  from "vuex"
    import axios from "axios"

    var {config : {payType}} = openMemberCommon;

    export default {
        props : ["type","value","memberConfig"],
        data(){
            return{
//                会员类型
                memberList : [
                    {
                        title : "钻石会员",
                        subTitle : "（电脑、手机和PAD）",
                        id : 1,
                        class : "video",
                        name : "钻石会员包",
                        isBigPack : true,
                        ...openMemberCommon.config.goodsInfo,
                        subList : [
                            {title : "20元/月",id : 5,subTitle : "每月到期前一天自动续费可随时取消",recommend : true,isContinuous : true,name : "钻石会员连续包月",payType : payType.SUNNY_V6_MOBILE_BOSS.type,
                                info : {
                                    pricing : 20,
                                }
                            },
                            {title : "20元",id : 1,subTitle : "仅需20/月",isContinuous : false,recommend : true,name : "钻石会员月包",payType : payType.QRCodePay.type},
                            {title : "55元",id : 2,subTitle : "仅需55/季",isContinuous : false,name : "钻石会员季包",payType : payType.QRCodePay.type},
                            {title : "100元",id : 3,subTitle : "仅需100/半年",isContinuous : false,name : "钻石会员半年包",payType : payType.QRCodePay.type},
                            {title : "175元",id : 4,subTitle : "仅需175/年",isContinuous : false,name : "钻石会员年包",payType : payType.QRCodePay.type},
                        ]
                    },
                    {
                        title : "咪咕PP体育特色包",
                        subTitle : "（电脑、手机和PAD）",
                        id : 2,
                        class : "sport",
                        name : "咪咕PP体育特色包",
                        isBigPack : false,
                        subList : [
                            // {title : "英超赛事包",subTitle : "33连续包月",recommend : true,name : "英超连续包月",goodsId : "PPyingchao",id : 1, goodsType : "MIGU_SPORTS_MEMBER",
                            //     info : {
                            //         pricing : 33,
                            //     }},
                            // {title : "欧洲足球通",subTitle : "50连续包月",recommend : true,name : "欧洲足球通连续包月",goodsId : "PPouzhou",id : 2, goodsType : "MIGU_SPORTS_MEMBER",
                            //     info : {
                            //         pricing : 50,
                            //     }},
                        ]
                    },
                ],
//              选择的会员类型
                memberType : 2,

                adv : "",
//                {id : 1000,wider : true,img : "http://img.cmvideo.cn:8080/publish/noms/2019/01/02/59657.jpg",url : ""}
//               产品类型
                goodsList : [
//                    {title : "包月18",subTitle : "18元/月",id : 2,recommend : true},
                ],
//                选择的产品类型
                goodsType : 5,

//                所有的支付方式列表
                payTypeList : [
//                    {title : "话费支付",isContinuous : false,id : 1,class : "HF",...openMemberCommon.config.payType.SUNNY_V6_MOBILE_BOSS},
                    {title : "话费支付",isContinuous : true,id : 2,class : "HF",...openMemberCommon.config.payType.SUNNY_V6_MOBILE_BOSS},
                    {title : "扫码支付",isContinuous : false,id : 3,class : "QRCode",...openMemberCommon.config.payType.QRCodePay},
                ],
//                体育包二维码支付
                fixSportsPay : {title : "扫码支付",isContinuous : true,id : 4,class : "fixQRCode"},
                goodsPayTypeList : [],
                payType : "",
//                话费支付的地址
                payUrl : "",
//                聚合二维码地址
                QRCode : "",
                isPay : true,
                imgUrl : "",

                advPageId : "d1dc32d8fed04198ba57e55e47adb830",
            }
        },
        computed:{
            ...mapState("userdata",["isLogin","userInfo","userHeaderInfo","memberInfo"]),
            getPayTextInfo(){
                var detail = this.findGoodsInfoByGoodsType();
                if(detail){
                    return detail.info;
                }
            }
        },
        watch : {
            value : {
                immediate : true,
                handler(n){
                    if(n&&this.isLogin){
                        if(this.getPageDataPro){
                            this.getPageDataPro.then(res=>{
                                this.initParam()._init();
                            })
                        }
                        else{
                            this.initParam()._init();
                        }

                    }
                }
            }
        },
        components : {
            PModal,
            openMemberModalHeader,
            memberTypeTab,
            memberTypeTabDetail,
            memberOpen,
            notLogin
        },
        created(){
//            this.initParam()._init();
            this.getPageDataPro = this.getPageData();
        },
        methods : {
            async _init(){
                try {
                    await this.typeTabChange(true);
                    await this.typeTabDetailChange();
                }
                catch (err){
                    console.log(err)
                }
            },
            advJump(){
                if(this.adv&&this.adv.url){
                    location.href = this.adv.url
                }
            },
            getPageData(){
                return new Promise((reso,rej)=>{
                    this.$fetch(api.PAGE_LAYOUT_PATH + this.advPageId).then(res=>{

                        try{
                            var label = res.body.groups[0].components[0].extraData.labels[0];
                            var url = label.action&&label.action.params&&label.action.params.url
                            var imgUrl = label.icon;
                            this.adv = {id : 1000,wider : true,img : imgUrl,url : url}
                        }catch(e){

                        }

                        try {
                            this.memberList[1].subList = res.body.groups[1].components[0].extraData;
                        }catch (err){

                        }

                        reso(res)
                    }).catch(err=>{
                        reso(null)
                    })
                })

            },
            typeTabChange(save){
                if(save !== true){
                    this.goodsType = "";
                    this.payType = "";
                }
                this.goodsList = [];
                return new Promise((reso,rej)=>{
                    this.initTypeChange().then(res=>{
                        if(this.adv){
                            this.goodsList =  [this.adv,...res]
                        }
                        else{
                            this.goodsList =  res
                        }
                        reso(this.goodsList);
                    }).catch(err=>{
                        console.log(err)
                        rej(err);
                    })
                })
            },

            typeTabDetailChange(){

                this.payType = "";
                this.imgUrl = "";
                this.QRCode = ""
                this.source&&this.source.cancel('cancelRequest');

                return new Promise((reso,rej)=>{
                    var goodsDetail = this.findGoodsInfoByGoodsType()
                    if(goodsDetail.wider){
                        this.isPay = false;
                        this.imgUrl = goodsDetail.img
//                        rej("图片")
                    }
                    else{
                        this.isPay = true;
                        this.initGoodsChange().then(res=>{
                            this.goodsPayTypeList =  res
                            reso(res)
                        }).catch(err=>{
                            rej(err)
                        })
                    }

                })
            },
            payTypeChange(){
                this.toPay();
            },
            initParam(){
                if(this.memberConfig){
                    this.memberType = this.memberConfig.memberType
                    this.goodsType = this.memberConfig.goodsType
                }
                return this;
            },

            findMemberTypeById(){
                let id = this.memberType;
                for (let i = 0 ; i < this.memberList.length ; i++){
                    if(this.memberList[i].id == id){
                        return this.memberList[i];
                    }
                }
                return null;
            },
            findGoodsInfoByGoodsType(){
                let id = this.goodsType;
                for (let i = 0 ; i < this.goodsList.length ; i++){
                    if(this.goodsList[i].id == id){
                        return this.goodsList[i];
                    }
                }
                return null;
            },
            findPayMentById(){
                let id = this.payType;
                for (let i = 0 ; i < this.goodsPayTypeList.length ; i++){
                    var _item = this.goodsPayTypeList[i];

                    if(_item.id == id){
                        return _item;
                    }
                }
                return null;
            },
//          tab_1  当会员类型改变的时候，触发事件（如果是大产品包，则开始批价）
            async initTypeChange(){
                var memberItem = this.findMemberTypeById()
                if(memberItem.isBigPack){
                    var goodsInfo = {
                        goodsId : memberItem.goodsId,
                        goodsType : memberItem.goodsType,
                        isBigPack : memberItem.isBigPack,
                        subType : memberItem.subList
                    }

                    var res = await openMemberCommon.pricing(goodsInfo);
                    let newList = this.payTypeListMap(res.goodsInfo);
                    return newList;
                }
                else{
                    return memberItem.subList
                }
            },
            payTypeListMap(pricingGoodsInfo){
                var {subType,goodsType,goodsId,isBigPack} = pricingGoodsInfo
                var _allPayList = [];

                for(let i = 0 ; i < subType.length; i ++){
                    let _list = [];
                    let _item = subType[i];
                    for(let j = 0 ; j < this.payTypeList.length; j ++){
                        let _items = this.payTypeList[j]

                        if(_item.payType == _items.type && _item.isContinuous == _items.isContinuous){
                            var _obj = {
                                ..._items,
                                payInfo : {
                                    goodsInfo : {
                                        goodsId,
                                        goodsType,
                                        payType : _item.payType,
                                    },
                                    serviceInfo : _item.payInfo.packItem.serviceInfo,
                                    mainDeliveryItem : _item.payInfo.packItem.mainDeliveryItem,
                                    extDeliveryItems : _item.payInfo.packItem.extDeliveryItems,
                                    returnUrl: location.href,
                                    amount: _item.payInfo.packPayItem.amount,
                                    charge : _item.payInfo.packPayItem.charge
                                }
                            }
                            _list.push(_obj)
                            _allPayList.push(_obj);
                            break;
                        }
                    }
                    _item.payMentList = _list;
                }
                if(isBigPack){
                    return subType
                }
                else{
                    return _allPayList;
                }
            },
//            tab_2 初始化（如果为小包则开始批价）
            async initGoodsChange(){
                var memberItem = this.findMemberTypeById()
                var goodsDetail = this.findGoodsInfoByGoodsType()
                if(memberItem.isBigPack){
                    return goodsDetail.payMentList;
                }
                else{
                    var _list = this.payTypeList.map(item=>{
                        return {
                            ...item,
                            name : goodsDetail.name,
                            payType : item.type
                        }
                    })
                    let goodsInfo = {
                        ...goodsDetail,
                        subType : _list
                    }
                    var res = await openMemberCommon.pricing(goodsInfo);
                    let newList = this.payTypeListMap(res.goodsInfo);

                    newList = [...newList,this.fixSportsPay];

                    return newList
                }
            },
            toPay(){
                let payMent = this.findPayMentById();

                if(!payMent.payInfo){
                    return;
                }

//                生成订单、开始支付
                this.source = axios.CancelToken.source();
                openMemberCommon.pay(payMent.payInfo,{
                    cancelToken : this.source.token
                }).then(res=>{
//                    话费支付
                    if(payMent.type == openMemberCommon.config.payType.SUNNY_V6_MOBILE_BOSS.type){
                        try {
                            this.payUrl = res.body.order.currentPaymentInfo.subPaymentInfoList[0].extData.webPayUrl
                        }
                        catch (e){
                        //    this.$Toast("下单失败，请稍后再试")
                           this.$Toast&&this.$Toast("下单失败，请稍后再试") || alert("下单失败，请稍后再试")
                        }
                    }
//                    二维码支付
                    else {
                        this.QRCode = res.body.order.currentPaymentInfo.subPaymentInfoList[0].extData.qrCodeUrl
                    }
                }).catch(err=>{
                    if(err.message == "cancelRequest"){

                    }
                    else{
                        // this.$Toast("网络错误，请刷新重试！")
                        this.$Toast&&this.$Toast("网络错误，请刷新重试！") || alert("网络错误，请刷新重试！")
                    }
                })
            },
            input(value){
                this.$emit("input",value);
            },
            sure(){
            }
        }

    }
</script>