//对象和url参数的相互转换
var ObjAndUrlParamInterconversion = (function () {
    //对象变为字符串
    var objToUrlParamString = function (obj) {
        var html = [];
        for (var item in obj) {
            html.push(item + '=' + obj[item]);
        }
        return html.join('&');
    };
    //字符串变为对象
    var urlParamStringToObj = function (str) {
        if (str.indexOf('?') != -1) {
            str = str.split('?')[1];
        }

        str = str.split('&');
        var newObj = {};
        for (var i = 0; i < str.length; i++) {
            var strItem = str[i].split('=');
            newObj[strItem[0]] = strItem[1];
        }

        return newObj;
    };

    return function (param) {
        if (param.toString().indexOf('[object Object]') != -1) {
            return objToUrlParamString(param);
        } else if (typeof param == 'string') {
            return urlParamStringToObj(param);
        } else {
            // console.log("不支持的转换类型")
        }
    };
})();
