//action处理
function actionHandle(action) {
    if (!action) {
        return;
    }
    var type = action.type;
    var [pageId, path, cid, url] = [
        action.params.pageID,
        action.params.path,
        action.params.contentID,
        action.params.url
    ];
    var extraWrap = action.params.extra;
    switch (type) {
        //播放
        case allActionSkipType.jumpPlayDetail.value:
            if (pageId == allWebPage.playVODVideo.pageId) {
                window.open(allWebPage.playVODVideo.url + "?cid=" + cid)
            } else if (pageId == allWebPage.playLiveVideo.pageId) {
                window.open(allWebPage.playVODVideo.url + "?cid=" + cid)
            }
            break;
        case allActionSkipType.jumpH5PageWithWebView.value:
            if(url.indexOf("://")>-1){
                window.open(url)
            }else{
                console.log("url: ",url)
            }
            break;
        case allActionSkipType.jumpH5PageWithSysBrowser.value:
            if(url.indexOf("://")>-1){
                window.open(url)
            }else{
                console.log("url: ",url)
            }
            break;
        case allActionSkipType.refreshPage.value:
            if(path) {
                window.open('index.html?pageId='+ path)
            }
            window.location.reload()
            break;
        case allActionSkipType.jumpClientPage.value:
            console.log('客户端新页面')
            if(extraWrap && extraWrap.mgdbID){
                jumpDeatail({mgdbId:extraWrap.mgdbID});
            }else if(url){
                window.open(url)
            }else{
                // window.open(allWebPage.index.url + '?pageId='+ pageId);
                commonHandle(webCommon.INDEX_PAGE_ID,{pageId})
            }
            break;
        case allActionSkipType.jumpClientPageWithNativeWidget.value:
            if (location.href.indexOf('pageId') != -1) {
                window.open(delUrlParam(location.href, 'pageId') + 'pageId=' + path); 
            } else {
                window.open(webCommon.INDEX_PAGE_ID + '?pageId='+ path);
            }
            break; 
     
        default:
            // console.log("未知类型的事件")
            break;
    }
}

