export function jumpDeatail(param) {
    console.log(param);
    if (isClient) {
        let cid = param.cid;
        let type = param.type || '';
        if (type) {
            if (!!window.ActiveXObject || "ActiveXObject" in window) {
                window.external.Play(cid, 1);
            } else {
                migu_app.Play(cid, 1);
            }
        } else {
            if (!!window.ActiveXObject || "ActiveXObject" in window) {
                window.external.Play(cid, 0);
            } else {
                migu_app.Play(cid, 0);
            }
        }
        
    } else {
        let cid = param.cid;
        let type = param.type || '';
        let data = param.mgdbId;
        switch(type){
            case webCommon.SPORT_LIVE_DETAIL:
                window.open(`sportLive.html?mgdbId=${data}`);
                break;
            default:
                if (cid) {
                    window.open(`detail.html?cid=${cid}`);
                } else {
                    window.open(`detail.html?mgdbId=${data}`);
                }
                break; 
        }
    }
}
export function delUrlParam(url, ref) {
    if (url.indexOf(ref) == -1)
        return url;
    var arr_url = url.split('?');
    var base = arr_url[0];
    var arr_param = arr_url[1].split('&');
    var index = -1;
    for (var i = 0; i < arr_param.length; i++) {
        var paired = arr_param[i].split('=');
        if (paired[0] == ref) {
            index = i;
            break;
        }
    }
    if (index == -1) {
        return url;
    } else {
        arr_param.splice(index, 1);
        return base + "?" + arr_param.join('&');
    }
}
