import api from "common/api";
import COM_MAP from "common/compMap";
import { SPORT_COM_MAP } from "common/compMap";
import filterCopyRight from 'utils/filterCopyRight';

export const getGroupRequestList = function(groups){
    return groups.map( (group,index) => {
        return {
            path:api.GROUP_LAYOUT_PATH+group.id,
            key:group.id,
            index:index
        }
    })
}

export const dataGroupFilter = function(groups,isSportIndex){
    let comMap = isSportIndex?SPORT_COM_MAP:COM_MAP;
    let arr = [],
        group = null,
        components = null;
    for (let i = 0; i<groups.length; i++){
        group = groups[i];
        components = group.components;
        // 过滤分省信息
        if (group.fitArea[0] != '10000') continue;
        if (!components || !components.length) continue;
        components.forEach(item => {
            item.data = null;
            item.totalCount = -1;
            item.totalPage = -1;
            item.compName = comMap[item.compStyle];
        })
        group.isShow = false;
        arr.push(group);
        
    }
    return arr;
}

export const mergeComp = function(group,source, noFilter){
    let target = group.components;
    if(!target || !target.length) return null;
    if(!source || !source.length) return target;
    let tItem = null,
        dataList = null,
        sItem = null;
    for(let i = 0; i < target.length; i++){
        tItem = target[i];
        for (let j = 0; j < source.length; j++){
            sItem = source[j];
            if(tItem.id == sItem.id){
                if(sItem.data && sItem.data.length>0){
                    if (noFilter) {
                        dataList = sItem.data
                    }else{
                        dataList = sItem.data.filter((v) => {
                                return filterCopyRight(v);
                            });
                    }
                    tItem.data = dataList;
                    if(dataList.length) group.isShow = true;
                }
                sItem.totalCount && (tItem.totalCount = sItem.totalCount);
                sItem.totalPage && (tItem.totalPage = sItem.totalPage);
                break;
            }
        }
    }
}