import { fetch } from 'utils/axios';
import utils from 'utils/utils';
import api from "common/api";
import webCommon from 'common/webCommon.js';
let SR_KEY = "searchHistory";

function getSH() {
    let sl = utils.storage.get(SR_KEY) || null;
    if(sl){
        return JSON.parse(sl);
    }
    return null;
}

const state = {
    config: null,
    navList:null,
    hotResult:null,
    searchResult:null,
    historyResult:getSH(),
    searchWords:'',
    headerNavList: null,
    publicData: null,
    modalStatus : false,
    memberConfig : {
        memberType : 1,
        goodsType : 1,
    },
}
/**
 * 同步修改 
 */
const mutations = {
    setConfig (state, config){
        state.config = config;
        // console.log(config);
    },
    updateUser (state) {
        state.userInfo = userData
    },
    set_key_value(state, { key, value }) {
        state[key] = value;
    },
    openMemberModalAction(state,status){
        state.modalStatus = status;
    },
    updateMemberConfig(state,param){
        state.memberConfig = {...state.memberConfig,...param}
    },
    setHistoryResult( state, value ){
        let str = utils.storage.get(SR_KEY) || null;
        let items = [];
        if(str){
            items = JSON.parse(str);    
        };
        items.unshift({ "words":value});
        // 搜索记录去重
        var arr = []
        for(var i=0;i<items.length;i++){
            var flag = true;
            for(var j=0;j<arr.length;j++){
            　　if(items[i]["words"] == arr[j]["words"]){
            　　　　flag = false;
            　　};
            }; 
            if(flag){
            　　arr.push(items[i]);
            };
        };
        if(arr.length>4){
            arr.pop();
        }
        state.historyResult = arr;
        utils.storage.set(SR_KEY,JSON.stringify(arr));
    },
    clearHistoryResult(state) {
        state.historyResult = []
    },
    toSearch( state, value ){
        state.searchWords = value;
        var c_page = 1
        //搜索处理
        let url = window.location.href;
        if(url.indexOf('search.html') > 0) {
            location.href =  `search.html?keywords=${value}`;
        } else {
            window.open(`search.html?keywords=${value}`)
        }
        // location.href =  `search.html?keywords=${value}`;
        // window.open(`search.html?keywords=${value}`)
    },
    setCommonData(state, val) {
        state.publicData = val.val;
    },
    setHeaderNavData(state, {pageId, flag, cid, sports, personal, search, channelLibrary, rank, dataList, teamDetail, val, deliver}) {
        if (deliver) { // 有传参的
            state.headerNavList = [
                {title: deliver, link:"#", icon:false, text:true},
                {title:"频道", list:val, icon:true, text:false}
            ]
        } else { // 无传参的
            if (flag && !pageId) { // 首页
                state.headerNavList = [
                    {title:"电视剧", link:"#", icon:false, text:true, pageId: '121d155c2ce74e9f96e7fa80b502d062'},
                    {title:"电影", link:"#", icon:false, text:true, pageId: '4b9bb7142c584534a73e78a0ef368ae1'},
                    {title:"综艺", link:"#", icon:false, text:true, pageId: '72ab2f7507ce4970a6925546255145ed'},
                    {title:"频道", list:val, icon:true, text:false}
                ]
            } else { 
                if (sports || dataList || teamDetail) { 
                    state.headerNavList = [
                        {title:"体育", link:"#", icon:false, text:true},
                        {title:"频道", list:val, icon:true, text:false}
                    ]
                }
                else if (personal) {
                    state.headerNavList = [
                        {title:"个人中心", link:"#", icon:false, text:true},
                        {title:"频道", list:val, icon:true, text:false}
                    ]
                }
                else if (search) {
                    state.headerNavList = [
                        {title:"搜索", link:"#", icon:false, text:true},
                        {title:"频道", list:val, icon:true, text:false}
                    ]
                }
                else if (channelLibrary) {
                    state.headerNavList = [
                        {title:"片库", link:"#", icon:false, text:true},
                        {title:"频道", list:val, icon:true, text:false}
                    ]
                }
                else if (rank) {
                    state.headerNavList = [
                        {title:"排行", link:"#", icon:false, text:true},
                        {title:"频道", list:val, icon:true, text:false}
                    ]
                }
                else if (cid) { // 详情页
                    state.headerNavList = [
                        {title:"电视剧", link:"#", icon:false, text:true, pageId: '121d155c2ce74e9f96e7fa80b502d062'},
                        {title:"电影", link:"#", icon:false, text:true, pageId: '4b9bb7142c584534a73e78a0ef368ae1'},
                        {title:"综艺", link:"#", icon:false, text:true, pageId: '72ab2f7507ce4970a6925546255145ed'},
                        {title:"频道", list:val, icon:true, text:false}
                    ]
                } else {
                    if (pageId == webCommon.MOVIE_PAGE_ID) { // 频道页
                        state.headerNavList = [
                            {title:"电影", link:"#", icon:false, text:true},
                            {title:"频道", list:val, icon:true, text:false}
                        ]
                    } else if (pageId == webCommon.TV_PAGE_ID) {
                        state.headerNavList = [
                            {title:"电视剧", link:"#", icon:false, text:true},
                            {title:"频道", list:val, icon:true, text:false}
                        ]
                    } else if (pageId == webCommon.VARIETY_PAGE_ID) {
                        state.headerNavList = [
                            {title:"综艺", link:"#", icon:false, text:true},
                            {title:"频道", list:val, icon:true, text:false}
                        ]
                    } else if (pageId == webCommon.AMUSE_PAGE_ID) {
                        state.headerNavList = [
                            {title:"搞笑", link:"#", icon:false, text:true},
                            {title:"频道", list:val, icon:true, text:false}
                        ]
                    } else if (pageId == webCommon.SPORTS_PAGE_ID) {
                        state.headerNavList = [
                            {title:"体育", link:"#", icon:false, text:true},
                            {title:"频道", list:val, icon:true, text:false}
                        ]
                    } else if (pageId == webCommon.COMIC_PAGE_ID) {
                        state.headerNavList = [
                            {title:"动漫", link:"#", icon:false, text:true},
                            {title:"频道", list:val, icon:true, text:false}
                        ]
                    } else if (pageId == webCommon.ENTERTAINMENT_PAGE_ID) {
                        state.headerNavList = [
                            {title:"娱乐", link:"#", icon:false, text:true},
                            {title:"频道", list:val, icon:true, text:false}
                        ]
                    } else if (pageId == webCommon.ORIGINAL_PAGE_ID) {
                        state.headerNavList = [
                            {title:"原创", link:"#", icon:false, text:true},
                            {title:"频道", list:val, icon:true, text:false}
                        ]
                    } else { 
                        state.headerNavList = [
                            {title:"频道", list:val, icon:true, text:true}
                        ]
                    }
                }
            }
        }
    }
}

/**
 * 计算属性
 */
const getters = {
    logo : state=>{
        let obj = state.config.logo;
        return obj;
    },

}

/**
 * 异步请求
 */
const actions = {
    getConfig ({ commit }){
        //获取数据
        fetch(api.HEADER_CONFIG_PATH).then(res=>{
            let data = JSON.parse(res.body.paramValue)[0].topBars;
            // console.log(data,'data,111111111111')
            for (let i = 0; i < data.length; i++) {
                if (data[i].id == "default-topbar") {
                    commit( "setConfig", data[i] );
                    break;
                }
            }
        },err=>{
            console.log(err);
        })
    },
    getHotResult({commit}){
        //获取数据
        fetch(api.HOT_KEY_WORD).then(res=>{
            let data = res.body.searchResult;
            if(data && data.length>10){
                data =  data.splice(0,10);
            }
            commit("set_key_value",{key:"hotResult",value:data});
        },err=>{
            console.log(err);
        })
    },
    getSearchTipResult({commit},value){
        //获取数据
        console.log(value);
        let url = `${api.TIP_KEY_WORD}${value}/poms/101?packId=1002581,1002601,1003341,1003861,1003862,1003863,1003864,1003865,1003866`
    
        fetch(url).then(res=>{
            let data = res.body.tipKeyWords;
            if(data && data.length>10){
                data =  data.splice(0,10);
            }
            commit("set_key_value",{key:"searchResult",value:data});
        },err=>{
            console.log(err);
        })
    },
    getChannelData({commit}) { // 获取频道按钮展示的数据
        fetch(api.PAGE_LAYOUT_PATH + `e28d7bfb6bf44a4ab9586e5633d28737`).then(function(presentData) {
            const {body: {groups, name}} = presentData;
            if (!groups || !groups.length || !groups[0].components || !groups[0].components.length) return;
            let data = groups[0].components[0].extraData.menus;
            // console.log(data);
            commit('setCommonData', {val: data});
        });
    }

}

export default {
    namespaced: true,
    state,
    actions,
    getters,
    mutations
}