let personalCenterRouters = {
    all : {
        setting : {
            path : "/setting/", name : "账号设置",type : "settingSide",
            children : [
                {path : "/setting/personalInfo", name : "账号设置-信息设置"},
                {path : "/setting/changePassword", name : "账号设置-密码修改"},
            ],
        },
        memberCenter : {
            path : "/memberCenter", name : "会员中心",type : "memberSide"
        },
        collect : {
            path : "/collect", name : "我的看单",type : "collectSide"
        },
        history : {
            path : "/history", name : "观看记录",type : "historySide"
        },
        myFilm : {
            path : "/myFilm", name : "我的追剧",type : "filmSide"
        },
    },
    getList(){
        let order = ["setting","memberCenter","collect","myFilm","history"];
        let newList = [];

        order.forEach((value)=>{
            var item = personalCenterRouters.all[value];
            if(item)newList.push(item);
        })

        return newList;
    },
}

export {
    personalCenterRouters
}