<template>
	<div class="rank-wrapper" @click="channelHide">
        <IndexHeader @showLogin="goLogin()" :userData = "userData" :headerNavList="headerNavList" :markNav = "{flag, isChange}"></IndexHeader>
		<div class="main-wraper">
			<loading v-if="loading.show" :data="loading"></loading>
			<error v-if="error.show" :data="error"></error>
            <div class="rank-title">
                <!-- <div class="txt-content">
                    <div class="txt">咪咕排行榜</div>
                    <div class="txt-footer">
                        <div class="line-l"></div>
                        <div class="con">RINKING</div>
                        <div class="line-r"></div>
                    </div>
                </div> -->
            </div>
			<div class="rank-content">
				<div v-for="item in groups"  :key="item.id" :data-id="item.id" :name="item.name" class="rank-group">
					<template v-if = "item.components && item.components.length">
						<div v-for="(comp,index) in item.components"  :key="index" :name="comp.name">
							<title-rank v-if="comp.compName == 'wlabel'" :key="index" :data="comp" :compName="item.name"></title-rank>
							<left-img-right-text-more v-else-if="comp.compName == 'leftImgRightText'" :key="index" :comp="comp" :groupID = "item.id"></left-img-right-text-more>
							<component v-else :is = "comp.compName"  :key="index" :name="comp.compName" :data="comp"></component>
						</div>
					</template>
				</div>
			</div>
		</div>
        <w-footer></w-footer>
		<float-btn></float-btn>
		<login-iframe v-if="showLoginIfr" @closeLogin="closeLoginIfr"></login-iframe>
	</div>
</template>

<style lang="less">
	@import "../../css/rank.less";
</style>

<script>
import {mapState, mapGetters, mapActions } from 'vuex';
import IndexHeader from "./group/header";
import wFooter from "./group/footer";
import loading from "./group/loading";
import error from "./group/error";
import titleRank from "./lib/title-rank";
import FloatBtn from "./lib/floatBtn";
import leftImgRightTextMore from "./lib/leftImgRightTextMore";
import loginIframe from 'components/group/loginIframe';
import Utils from '../utils/utils.js';
import KSStatistics from "KSStatistics"
export default {
	data() {
		return {
			flag: null,
			isChange: null
		}
	},
    computed: {
		...mapState({
			loading: state => state.loading,
			error: state=> state.error,
			groups: state=> state.groups,
			showLoginIfr: state=> state.showLoginIfr,
			userData: state=> state.userdata,
			headerNavList: state=>state.headerNavList
        }),
        ...mapGetters('header', {
            logo: 'logo'
        })
	},
	components:{
		IndexHeader,
		wFooter,
		loading,
        error,
        titleRank,
		leftImgRightTextMore,
		loginIframe,
		FloatBtn
	},
    created () {
        // this.$store.dispatch('index/pageConfig');
        // 埋点
        KSStatistics.setStaticParam({
            pageName : "榜单页",
        }).ready().PV();
	},
	mounted(){
		this.pageConfig();
		 
        window.addEventListener('message',e=>{
            console.log("message");
            // console.log(e);
            if(e && e.data && (typeof e.data) === 'string'){
				let d1 = JSON.parse(e.data);
                if(d1.type == "login"){
					this.closeLoginIfr();
					this.$store.commit('userdata/updateUserInfo');
					this.$store.dispatch('history/getPlayHistory', {
						start: 0,
						limit: 100,
						all: 1,
						prdInfoIds: 1002601
					});
                }
            }
        });
	},
	methods:{
		...mapActions(['pageConfig']),
		goLogin(){
			this.$store.commit('set_key_value',{key:'showLoginIfr',value:true})
		},
		closeLoginIfr(){
			this.$store.commit('set_key_value',{key:'showLoginIfr',value:false});
		},
		channelHide() {
			this.flag = true;
			this.isChange = !this.isChange;
			Utils.clickHide();
		}
	}
}
</script>



